<template>
  <div>
    <!--observações-->
    <v-alert color="blue lighten-3">

      <!--title e btn-->
      <v-row>

        <!--Title-->
        <v-col>
          <h2>Observações de Dependente </h2>
        </v-col>

        <!--Btn add obs-->
        <v-col class="text-right">
          <v-btn @click="openNewObs(editedDep[1].CP)" class="primary" small>
            <v-icon class="mr-5" small>mdi-message-plus</v-icon>
            Adicionar Obs
          </v-btn>
        </v-col>

      </v-row>

      <!--espaço para observacoes-->
      <v-row>

        <v-col>

          <!--DataTable-->
          <v-data-table
            :headers="headersObs"
            :items="editedDep[0].obs"
            :items-per-page="-1"
            class="elevation-21 mt-4"
            sort-by="nome"
          >

            <!--Template de ajustar datas de inclusão -->
            <template v-slot:item.created_at="{ item }">

              <span v-if="item.created_at === null">Aguardando envio ao servidor.</span><span v-else>{{ formataDataHora(item.created_at, 'Sim')}}</span>

            </template>

            <!--Template de botões para editar e excluir -->
            <template v-slot:item.actionsObs="{ item }">

              <!--editar-->
              <v-tooltip top v-if="usuarioLogado.id === item.user_id">
                <template v-slot:activator="{ on, attrs }">
                  <v-icon
                    @click="editObs(item)"
                    class="mr-2"
                    small
                    v-bind="attrs"
                    v-on="on"
                  >
                    mdi-pencil
                  </v-icon>
                </template>
                <span>Editar Observação</span>
              </v-tooltip>

              <!--Excluir-->
              <v-tooltip top v-if="usuarioLogado.id === item.user_id">
                <template v-slot:activator="{ on, attrs }">
                  <v-icon
                    @click="excluiObs(item)"
                    class="mr-2"
                    small
                    v-bind="attrs"
                    v-on="on"
                  >
                    mdi-delete
                  </v-icon>
                </template>
                <span>Excluir Observação</span>
              </v-tooltip>

            </template>

          </v-data-table>

          <!--Dialog para cadastrar / editar Obs-->
          <v-dialog max-width="70%" persistent v-model="dialogNewObs">

            <v-card>

              <!--title-->
              <v-card-title class="justify-center" primary-title>
                <v-icon
                  class="mr-4">
                  mdi-message-plus
                </v-icon>
                {{leTitleObsDialog}} de Observação

              </v-card-title>

              <v-card-text>

                <!--Obs-->
                <v-row>
                  <v-col>
                    <v-textarea
                      dense
                      label="Observação"
                      name="leObs"
                      outlined
                      placeholder="Insira a Observação"
                      required
                      v-model="leObs"
                    ></v-textarea>
                  </v-col>
                </v-row>

                <!--alert para obs em branco-->
                <v-row no-gutters v-if="leObs.length === 0">
                  <v-col>
                    <v-alert type="error">O Campo Observação não pode ficar em branco!</v-alert>
                  </v-col>
                </v-row>

              </v-card-text>

              <!--actions-->
              <v-card-actions class="pb-5">
                <v-spacer></v-spacer>

                <!--fechar-->
                <v-btn @click="dialogNewObs = false" color="grey lighten-1">Cancelar</v-btn>
                <span class="pl-5 pr-5"></span>

                <!--enviar-->
                <v-btn @click="submeteObs" class="success">Salvar</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>

            </v-card>

          </v-dialog>

          <!--Dialog para deletar obs-->
          <v-dialog max-width="800px" v-model="dialogDeleteObs">
            <v-card>
              <v-card-title class="justify-center" primary-title>
                <v-icon
                  class="mr-4">
                  fa fa-exclamation-triangle
                </v-icon>
                Você tem certeza que quer deletar esta Observação?
                <v-icon
                  class="ml-4">
                  fa fa-exclamation-triangle
                </v-icon>

              </v-card-title>
              <v-card-text>
                <div class="text-center">Essa ação é irreversível. Tenha certeza do que está fazendo.</div>
              </v-card-text>
              <v-card-actions class="pb-5">
                <v-spacer></v-spacer>
                <v-btn @click="dialogDeleteObs = false" color="grey lighten-1">Cancelar</v-btn>
                <span class="pl-5 pr-5"></span>
                <v-btn @click="deleteObsConfirm" color="red lighten-1">Excluir</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>

        </v-col>

      </v-row>

    </v-alert>
  </div>
</template>

<script>import moment from 'moment'
import {mapGetters} from 'vuex'

export default {
  data: vm => ({
    headersObs: [
      {
        text: 'Observação',
        align: 'start',
        value: 'observacao'
      },
      {
        text: 'Responsável',
        align: 'start',
        value: 'resp'
      },
      {
        text: 'Data/Hora',
        align: 'start',
        value: 'created_at'
      },
      {
        text: 'Actions',
        value: 'actionsObs',
        align: 'center',
        sortable: false
      }
    ],
    dialogNewObs: false,
    dialogDeleteObs: false,
    dialogEditObs: false,
    leObs: '',
    leTitleObsDialog: '',
    lecpdecujus: '',
    leidObs: -1,
    contadorTempObs: 0,
    objetoObs: {}
  }),
  computed: {
    ...mapGetters(['usuarioLogado'])
  },
  props: {
    editedDep: Array
  },
  methods: {
    openNewObs (cp) {
      this.lecpdecujus = cp
      this.leObs = ''
      this.leTitleObsDialog = 'Cadastro'
      this.dialogNewObs = true
      this.leidObs = -1
    },

    editObs (item) {
      this.lecpdecujus = item.cp
      this.leObs = item.observacao
      this.objetoObs = item
      this.leTitleObsDialog = 'Edição'
      this.dialogNewObs = true
      this.leidObs = item.id
    },

    submeteObs () {
      if (this.leObs.length !== 0) {
        let objetoParaEnvio = {}
        objetoParaEnvio['observacao'] = this.leObs
        objetoParaEnvio['dependente_id'] = this.editedDep[0].id
        if (this.editedDep[2] === -1) {
          objetoParaEnvio['created_at'] = null
          objetoParaEnvio['resp'] = this.usuarioLogado.guerra
          objetoParaEnvio['user_id'] = this.usuarioLogado.id
          if (this.leidObs === -1) {
            objetoParaEnvio['id'] = this.contadorTempObs + '_T'
            this.editedDep[0].obs.push(objetoParaEnvio)
            this.contadorTempObs++
          } else {
            objetoParaEnvio['id'] = this.objetoObs.id
            for (let i = 0; i < this.editedDep[0].obs.length; i++) {
              if (this.editedDep[0].obs[i].id === this.objetoObs.id) {
                this.editedDep[0].obs.splice(i, 1, objetoParaEnvio)
              }
            }
          }
          this.dialogNewObs = false
        } else {
          if (this.leidObs === -1) {
            this.$http.post('depobs', objetoParaEnvio)
              .then(response => {
                this.editedDep[0].obs.push(response.data)
                this.$toastr.s(
                  'Observação enviada com sucesso', 'Sucesso!'
                )
                this.dialogNewObs = false
              }, err => {
                console.log(err)
                this.$toastr.e(
                  'Não foi possível enviar a observação!', 'Erro!'
                )
              })
          } else {
            this.$http.put('depobs/' + this.leidObs, objetoParaEnvio)
              .then(response => {
                for (let i = 0; i < this.editedDep[0].obs.length; i++) {
                  if (this.editedDep[0].obs[i].id === this.leidObs) {
                    this.editedDep[0].obs.splice(i, 1, response.data)
                  }
                }
                this.$toastr.s(
                  'Observação alterada com sucesso', 'Sucesso!'
                )
              }, err => {
                console.log(err)
                this.$toastr.e(
                  'Não foi possível alterar a observação!', 'Erro!'
                )
              })
            this.dialogNewObs = false
          }
        }
      } else {
        this.$toastr.e(
          'É necessário escrever algo no campo Observação', 'Erro!'
        )
      }
    },

    deleteObsConfirm () {
      if (this.objetoObs.id.toString().split('_').length > 1) {
        for (let i = 0; i < this.editedDep[0].obs.length; i++) {
          if (this.editedDep[0].obs[i].id === this.objetoObs.id) {
            this.editedDep[0].obs.splice(i, 1)
          }
        }
        this.dialogDeleteObs = false
      } else {
        this.$http.delete('depobs/' + this.objetoObs.id)
        // eslint-disable-next-line no-return-assign
          .then(() => {
            for (let i = 0; i < this.editedDep[0].obs.length; i++) {
              if (this.editedDep[0].obs[i].id === this.objetoObs.id) {
                this.editedDep[0].obs.splice(i, 1)
              }
            }
            this.$toastr.s(
              'Observação excluída com sucesso', 'Sucesso!'
            )
            this.dialogDeleteObs = false
          }, err => {
            console.log(err)
            this.$toastr.e(
              'Não foi possível excluir a observação!', 'Erro!'
            )
          })
      }
    },

    excluiObs (item) {
      this.objetoObs = item
      this.dialogDeleteObs = true
    },

    // utilitários

    formataDataHora (dataHora, ch) {
      if (ch === '' || ch === null) {
        ch = 'Não'
      }

      if (dataHora === null) {
        return null
      } else if (dataHora[2] === '/') {
        return dataHora
      } else {
        let d = moment.tz(dataHora, 'America/Manaus')
        let dh
        if (ch === 'Sim') {
          dh = d.format('DD/MM/YYYY ( HH:mm:ss )')
        } else {
          dh = d.format('DD/MM/YYYY')
        }
        return dh
      }
    }
  }
}
</script>



