<template>
  <Usuario :secoes="secoes" :usuarios="usuarios"/>
</template>

<script>import Usuario from '../components/Usuario.vue'
import {mapGetters} from 'vuex'

export default {
  components: {
    Usuario
  },
  data () {
    return {
      usuarios: [],
      secoes: []
    }
  },
  mounted () {
    this.$http.get('users')
      .then(response => {
        this.usuarios = response.data.data
      })
      .catch(erro => console.log(erro))

    this.$http.get('secoes')
      .then(response => {
        this.secoes = response.data.data
      })
      .catch(erro => console.log(erro))
  },
  computed: {

    ...mapGetters(['usuarioLogado'])

  }
}
</script>



