        </v-card-title>

        <v-card-text>

          <ul>
            <li>Versão 1.1 A (alpha) - 07/06/2022
              <ul>
                <li>Agora é possivel editar e cadastrar UA Pagadora</li>
                <li>Agora é possível editar e cadastrar Posto/Grad</li>
                <li>Foi retirada a obrigatoriedade de inserção dos seguintes itens:</li>
              </ul>
            </li>
            <li>Versão 1.0 A (alpha) - 06/10/2021
              <ul>
                <li>É colocado em produção dia 06 de outubro de 2021</li>
                <li>Utiliza Vue no front e Lumen no back</li>
                <li>Início da etapa de testes</li>
