<template>
  <Secao :secoes="secoes" :oms="oms" :dataBtnPai="dataBtnPai"  @resetarPais="dataBtnPai = $event"/>
</template>

<script>import {mapGetters} from 'vuex'
import Secao from '../components/Secao'

export default {
  components: {
    Secao
  },
  data () {
    return {
      secoes: [],
      oms: [],
      dataBtnPai: []
    }
  },
  mounted () {
    this.$http.get('secoes')
      .then(response => {
        this.secoes = response.data.data
      })
      .catch(erro => console.log(erro))

    this.$http.get('oms')
      .then(response => {
        this.oms = response.data
      })
      .catch(erro => console.log(erro))

    this.$http.get('secoes/lepais')
      .then(response => {
        this.dataBtnPai = response.data
      })
      .catch(erro => console.log(erro))
  },
  computed: {

    ...mapGetters(['usuarioLogado'])

  }
}
</script>



