import Vue from 'vue'
import VueRouter from 'vue-router'
import Usuarios from '../views/Usuarios.vue'
import Home from '../views/Home.vue'
import Secoes from '../views/Secoes.vue'
import Processos from '../components/processos/Processos.vue'
import Inativos from '../components/decujos/Inativo.vue'
import erro500 from '../views/erro500.vue'
import erroToken from '../views/erroToken.vue'
// import Reset from '../views/Reset.vue'
import Login from '../views/Login.vue'
import store from '@/store'
import Oms from '../views/Oms'
import Dependente from '../components/decujos/Dependente'
import Locais from '../views/Locais'
import PostoGrads from '../views/PostoGrads'

Vue.use(VueRouter)

const routes = [
  {
    path: '',
    name: 'home',
    component: Home,
    meta: {
      logado: true
    }
  },
  {
    path: '/erro500',
    name: 'erro500',
    component: erro500,
    meta: {
      logado: true
    }
  },
  {
    path: '/erroToken',
    name: 'erroToken',
    component: erroToken,
    meta: {
      logado: true
    }
  },
  {
    path: '*',
    component: Home
  },
  {
    path: '/reset',
    name: 'reset',
    meta: {
      logado: true
    },
    // component: Reset
    component: () => import(/* webpackChunkName: "Reset" */ '../views/Reset.vue')
  },
  {
    path: '/usuarios',
    name: 'usuarios',
    component: Usuarios,
    meta: {
      adminGeral: true,
      admin: false,
      delegado: false,
      homologador: false,
      executor: false,
      visualizador: false
    }
  },
  {
    path: '/secoes',
    name: 'Seções',
    component: Secoes,
    meta: {
      adminGeral: true,
      admin: false,
      delegado: false,
      homologador: false,
      executor: false,
      visualizador: false
    }
  },
  {
    path: '/oms',
    name: 'Oms',
    component: Oms,
    meta: {
      adminGeral: true,
      admin: false,
      delegado: false,
      homologador: false,
      executor: false,
      visualizador: false
    }
  },
  {
    path: '/decujos',
    name: 'Inativos',
    component: Inativos,
    meta: {
      adminGeral: true,
      admin: true,
      delegado: false,
      homologador: false,
      executor: false,
      visualizador: true
    }
  },
  {
    path: '/dependentes',
    name: 'Dependente',
    component: Dependente,
    meta: {
      adminGeral: true,
      admin: true,
      delegado: false,
      homologador: false,
      executor: false,
      visualizador: true
    }
  },
  {
    path: '/processos',
    name: 'Processos',
    component: Processos,
    meta: {
      adminGeral: true,
      admin: true,
      delegado: false,
      homologador: false,
      executor: false,
      visualizador: true
    }
  },
  {
    path: '/locais',
    name: 'Locais',
    component: Locais,
    meta: {
      adminGeral: true,
      admin: true,
      delegado: false,
      homologador: false,
      executor: false,
      visualizador: false
    }
  },
  {
    path: '/postograd',
    name: 'PostoGrad',
    component: PostoGrads,
    meta: {
      adminGeral: true,
      admin: true,
      delegado: false,
      homologador: false,
      executor: false,
      visualizador: false
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      publica: true
    }
  }

]
const router = new VueRouter({
  routes,
  mode: 'history'
})
router.beforeEach((routeTo, routeFrom, next) => {
  if (!routeTo.meta.publica && !store.state.token) {
    return next({path: '/login'})
  }

  if (routeTo.meta.logado && Object.values(store.state.usuarioLogado).length === 0) {
    return next({path: '/login'})
  }

  if (Object.values(store.state.usuarioLogado).length > 0) {
    if (!routeTo.meta.logado) {
      let arrayDeMeta = []
      let arrayDePermissao = []
      let somaDeIgualdade = 0
      arrayDeMeta.push(routeTo.meta.adminGeral)
      arrayDeMeta.push(routeTo.meta.admin)
      arrayDeMeta.push(routeTo.meta.delegado)
      arrayDeMeta.push(routeTo.meta.homologador)
      arrayDeMeta.push(routeTo.meta.executor)
      arrayDeMeta.push(routeTo.meta.visualizador)

      for (let i = 0; i < 6; i++) {
        if (i === 0) {
          if (store.state.usuarioLogado.permissoes.indexOf('Administrador Geral') !== -1) {
            arrayDePermissao.push(true)
          } else {
            arrayDePermissao.push(false)
          }
        }
        if (i === 1) {
          if (store.state.usuarioLogado.permissoes.indexOf('Administrador') !== -1) {
            arrayDePermissao.push(true)
          } else {
            arrayDePermissao.push(false)
          }
        }
        if (i === 2) {
          if (store.state.usuarioLogado.permissoes.indexOf('Delegado') !== -1) {
            arrayDePermissao.push(true)
          } else {
            arrayDePermissao.push(false)
          }
        }
        if (i === 3) {
          if (store.state.usuarioLogado.permissoes.indexOf('Homologador') !== -1) {
            arrayDePermissao.push(true)
          } else {
            arrayDePermissao.push(false)
          }
        }
        if (i === 4) {
          if (store.state.usuarioLogado.permissoes.indexOf('Executor') !== -1) {
            arrayDePermissao.push(true)
          } else {
            arrayDePermissao.push(false)
          }
        }
        if (i === 5) {
          if (store.state.usuarioLogado.permissoes.indexOf('Visualizador') !== -1) {
            arrayDePermissao.push(true)
          } else {
            arrayDePermissao.push(false)
          }
        }
      }

      for (let i = 0; i < 6; i++) {
        if (arrayDeMeta[i] === arrayDePermissao[i] && (arrayDeMeta[i] === true && arrayDePermissao[i] === true)) {
          somaDeIgualdade++
        }
      }

      if (somaDeIgualdade === 0) {
        return next({path: '/'})
      }
    }
  }
  next()
})

export default router



