<template>
  <PostoGrad :pg="pg"/>
</template>

<script>import {mapGetters} from 'vuex'
import PostoGrad from '../components/postograd/PostoGrad'

export default {
  components: {
    PostoGrad
  },
  data () {
    return {
      pg: []
    }
  },
  mounted () {
    this.$http.get('postograd')
      .then(response => {
        this.pg = response.data
      })
      .catch(erro => console.log(erro))
  },
  computed: {

    ...mapGetters(['usuarioLogado'])

  }
}
</script>



