<template>

  <v-main>

    <v-container class="pt-5">

      <!--Banner-->
      <v-banner
        class="p-5"
        elevation="21">

        <v-icon
          size="36"
          slot="icon">
          mdi-home-map-marker
        </v-icon>

        <h2>Gerenciamento de Organizações Militares </h2>

      </v-banner>

      <!--DataTable-->
      <v-data-table
        :headers="headers"
        :items="oms"
        :search="search"
        class="elevation-21 mt-4"
      >
        <template v-slot:top>

          <v-toolbar
            flat>

            <v-toolbar-title>Tabela de Oms Cadastradas</v-toolbar-title>

            <v-divider
              class="mx-4"
              inset
              vertical></v-divider>

            <!--Dialog de cadastro e edição-->
            <v-dialog
              max-width="50%"
              persistent v-model="dialog">

              <!--novo cadastro-->
              <template v-slot:activator="{ on, attrs }">

                <v-btn
                  class="mb-2"
                  color="primary"
                  dark
                  v-bind="attrs"
                  v-on="on">

                  <v-icon class="mr-5" small>
                    mdi-message-plus-outline
                  </v-icon>

                  Nova Om

                </v-btn>

              </template>

              <!--Card de cadastro edição-->
              <v-form>

                <v-card>

                  <v-card-title>

                    <v-icon class="mr-4">
                      fab fa-fort-awesome-alt
                    </v-icon>
                    <span class="headline">
                       {{ formTitle }}</span>

                  </v-card-title>

                  <v-card-text>

                    <v-container>

                      <v-row>

                        <!--nome - sigla  -->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4">

                          <!--Nome-->
                          <v-row>

                            <v-col>

                              <v-text-field
                                :error-messages="nomeErrors"
                                @blur="$v.nome.$touch()"
                                @input="$v.nome.$touch()"
                                dense
                                hint="Por exemplo: Centro de Embarcações do Comando Militar da Amazônia"
                                label="Nome"
                                name="nome"
                                outlined
                                placeholder="Insira o Nome da Om"
                                required
                                v-model="nome">

                              </v-text-field>

                            </v-col>

                          </v-row>

                          <!--sigla-->
                          <v-row>

                            <v-col>

                              <v-text-field
                                :error-messages="siglaErrors"
                                @blur="$v.sigla.$touch()"
                                @input="$v.sigla.$touch()"
                                dense
                                hint="Por exemplo: CECMA"
                                label="Sigla da Om"
                                name="sigla"
                                outlined
                                placeholder="Insira a sigla da Om"
                                required
                                v-model="sigla">
                              </v-text-field>

                            </v-col>

                          </v-row>

                          <!--om pai-->
                          <v-row>
                            <v-col>
                              <v-select
                                :error-messages="OmPaiErrors"
                                :items="omsAjustadas"
                                @blur="$v.ompai.$touch()"
                                @input="$v.ompai.$touch()"
                                dense
                                hint="Selecione a Om a qual esta estará subordinada"
                                item-text="sigla"
                                item-value="id"
                                label="Om Pai"
                                name="om"
                                outlined
                                required
                                return-object
                                v-model="ompai"
                              ></v-select>
                            </v-col>
                          </v-row>

                          <v-row>
                            <v-col>
                            <v-alert type="warning">
                              Seções Vinculadas
                              <ul>
                                <li :key="leseccs.id" v-for="leseccs in editedOm.secoes"> {{ leseccs.sigla}}</li>
                              </ul>
                            </v-alert>
                            </v-col>
                          </v-row>

                        </v-col>
                        <!--COR-->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4">

                          <v-card
                            elevation="2">

                            <v-container fluid>

                              <v-row>

                                <v-col>
                                  <p><b>Cor de referencia da Om</b></p>
                                  <v-color-picker
                                    canvas-height="150"
                                    hide-inputs
                                    mode="hexa"
                                    name="cor"
                                    v-model="cor"
                                    width="500">
                                  </v-color-picker>

                                  <v-alert type="info">
                                    Apenas para gerenciamento de estatísticas.
                                  </v-alert>

                                </v-col>

                              </v-row>

                            </v-container>

                          </v-card>

                        </v-col>

                      </v-row>

                      <!--Botões-->
                      <v-row>

                        <v-col>

                          <!--Salvar-->
                          <v-btn
                            @click="save"
                            color="green darken-1">
                            Salvar
                          </v-btn>

                          <!--Cancelar-->
                          <v-btn
                            @click="close"
                            class="ml-6"
                            color="red darken-1">
                            Cancelar
                          </v-btn>

                        </v-col>

                      </v-row>

                    </v-container>

                  </v-card-text>

                </v-card>

              </v-form>

            </v-dialog>

            <v-spacer></v-spacer>

            <!--Pesquisar-->
            <v-text-field
              append-icon="mdi-magnify"
              hide-details
              label="Pesquisar"
              placeholder="Pesquisar"
              single-line
              v-model="search">
            </v-text-field>

            <!--Dialog para deletar Om-->
            <v-dialog max-width="800px" v-model="dialogDelete">

              <v-card>

                <v-card-title class="justify-center" primary-title>

                  <v-icon class="mr-4">fa fa-exclamation-triangle</v-icon>
                  Você tem certeza que quer deletar esta Organização Militar?
                  <v-icon class="ml-4">fa fa-exclamation-triangle</v-icon>

                </v-card-title>

                <v-card-text>
                  <v-row>
                    <v-col class="text-center">
                      <p>Essa ação é irreversível. Tenha certeza do que está fazendo.</p>

                      <div class="text-left" v-if="editedOm.secoes.length > 0">
                        <p>As seguintes seções também serão excluídas:</p>
                        <ul>
                          <li :key="leseccs.id" v-for="leseccs in editedOm.secoes"> {{ leseccs.sigla}}</li>
                        </ul>
                      </div>
                    </v-col>
                  </v-row>
                </v-card-text>

                <v-card-actions class="pb-5">

                  <v-spacer></v-spacer>

                  <v-btn
                    @click="closeDelete"
                    color="grey lighten-1">
                    Cancelar
                  </v-btn>

                  <span class="pl-5 pr-5"></span>

                  <v-btn
                    @click="deleteOmConfirm"
                    color="red lighten-1">
                    Excluir
                  </v-btn>

                </v-card-actions>

              </v-card>

            </v-dialog>

          </v-toolbar>

        </template>

        <!--Template de botões para editar e excluir -->
        <template v-slot:item.actions="{ item }">

          <!--editar-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="editOm(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on">
                mdi-pencil
              </v-icon>
            </template>
            <span>Editar Om</span>
          </v-tooltip>

          <!--Excluir-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="deleteOm(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on">
                mdi-delete
              </v-icon>
            </template>
            <span>Excluir Om</span>
          </v-tooltip>
        </template>

        <!--Ajuste de cores-->
        <template v-slot:item.cor="{ item }">
          <v-chip
            :color="getColor(item.cor)"
            dark>
          </v-chip>
        </template>

      </v-data-table>

      <!--Organograma-->
      <v-card class="mt-5" elevation="21">
        <v-card-title>Organograma</v-card-title>
        <v-card-text>

          <!--Botões de organograma-->

          <v-row>
            <v-col :key="lebtn.id" cols="3" v-for="lebtn in dataBtnPai">
              <v-btn :color="lebtn.cor" @click="showOrgOm(lebtn.id)">{{lebtn.sigla}}</v-btn>
            </v-col>
          </v-row>

          <v-row v-if="dataChart.title !== 'Selecione uma opção'">
            <v-col class="btn-wrap">
              <v-btn @click="closeChart" class="error">
                <v-icon class="mr-3">
                  mdi-close-circle-outline
                </v-icon>
                Fechar
              </v-btn>
              <organization-chart :datasource="dataChart" pan></organization-chart>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>

    </v-container>

  </v-main>

</template>
<script>import {validationMixin} from 'vuelidate'
import {required} from 'vuelidate/lib/validators'
import OrganizationChart from 'vue-organization-chart'
import 'vue-organization-chart/dist/orgchart.css'

export default {
  components: {
    OrganizationChart
  },
  mixins: [validationMixin],
  directives: {},
  validations: {
    nome: {required},
    sigla: {required},
    ompai: {required}
  },

  data: () => ({
    // Itens de tabelas
    headers: [
      {
        text: 'Nome',
        align: 'start',
        value: 'nome'
      },
      {
        text: 'Sigla',
        value: 'sigla'
      },
      {
        text: 'Om Pai',
        value: 'pai.sigla'
      },
      {
        text: 'Cor',
        value: 'cor',
        align: 'center'
      },
      {
        text: 'Actions',
        value: 'actions',
        align: 'center',
        sortable: false
      }
    ],
    search: '',
    // Para edição e cadastro
    dialog: false,
    editedIndex: -1,
    nome: '',
    sigla: '',
    cor: '',
    dataChart: {
      id: 1,
      title: 'Selecione uma opção',
      name: 'Selecione uma opção',
      children: []
    },
    // para deletar
    dialogDelete: false,
    defaultOm: {
      nome: '',
      sigla: '',
      cor: '',
      pai: '',
      om_pai: '',
      secoes: []
    },
    editedOm: {
      nome: '',
      sigla: '',
      cor: '',
      pai: '',
      om_pai: '',
      secoes: []
    },
    // subordinacao
    ompai: '',
    pai: '',
    omsAjustadas: []
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nova Om' : 'Editar Om'
    },
    nomeErrors () {
      const errors = []
      if (!this.$v.nome.$dirty) return errors
      !this.$v.nome.required && errors.push('O Campo "Nome" não pode ficar em branco! ')
      return errors
    },
    siglaErrors () {
      const errors = []
      if (!this.$v.sigla.$dirty) return errors
      !this.$v.sigla.required && errors.push('O Campo "Sigla" não pode ficar em branco! ')
      return errors
    },
    OmPaiErrors () {
      const errors = []
      if (!this.$v.ompai.$dirty) return errors
      !this.$v.ompai.required && errors.push('O Campo "Om Pai" não pode ficar em branco! ')
      return errors
    },
    showColor () {
      if (typeof this.cor === 'string') return this.cor

      return JSON.stringify(Object.keys(this.cor).reduce((cor, key) => {
        cor[key] = Number(this.cor[key].toFixed(2))
        return cor
      }, {}), null, 2)
    }
  },

  watch: {
    dialog (val) {
      if (this.editedIndex === -1 && val) {
        this.omsAjustadas = []

        for (let i = 0; i < this.oms.length; i++) {
          this.omsAjustadas.push(this.oms[i])
        }

        this.omsAjustadas.push({
          cor: '#000000',
          id: 0,
          nome: 'Om Pai',
          pai: 0,
          om_pai: 0,
          sigla: 'Om Pai'
        })
      }
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
    cor (value) {
      // temporary fix while there is no way to disable the alpha channel in the colorpicker component: https://github.com/vuetifyjs/vuetify/issues/9590
      if (value.toString().match(/#[a-zA-Z0-9]{8}/)) {
        this.cor = value.substr(0, 7)
      }
    }
  },

  props: {
    oms: Array,
    dataBtnPai: Array
  },

  methods: {
    editOm (item) {
      this.editedIndex = this.oms.indexOf(item)
      this.editedOm = Object.assign({}, item)
      this.id = this.editedOm.id
      this.nome = this.editedOm.nome
      this.sigla = this.editedOm.sigla
      this.ompai = this.editedOm.pai
      this.cor = this.editedOm.cor
      this.omsAjustadas = []
      let arraySecAjust = []
      for (let i = 0; i < this.oms.length; i++) {
        if (this.oms[i].id === this.id) {
          arraySecAjust.push({
            cor: this.oms[i].cor,
            id: this.oms[i].id,
            nome: this.oms[i].nome,
            sigla: 'Om Pai'
          })
        } else {
          arraySecAjust.push(this.oms[i])
        }
      }
      this.omsAjustadas = arraySecAjust
      this.$v.$touch()
      this.dialog = true
    },

    getColor (cor) {
      return cor
    },

    deleteOm (item) {
      this.editedIndex = this.oms.indexOf(item)
      this.editedOm = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteOmConfirm () {
      this.$http.delete('oms/' + this.editedOm.id)
      // eslint-disable-next-line no-return-assign
        .then(() => {
          this.oms.splice(this.editedIndex, 1)
          this.resetFields()
          this.$toastr.s(
            'Om removida com sucesso', 'Sucesso!'
          )
          this.closeDelete()
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover a Om', 'Erro!'
          )
        })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedOm = Object.assign({}, this.defaultOm)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    resetFields () {
      this.id = ''
      this.nome = ''
      this.sigla = ''
      this.ompai = ''
      this.pai = ''
      this.cor = ''
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedOm = Object.assign({}, this.defaultOm)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    save () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        let objetoParaEnvio = {}
        objetoParaEnvio['id'] = this.id
        objetoParaEnvio['nome'] = this.nome
        objetoParaEnvio['sigla'] = this.sigla
        objetoParaEnvio['ompai'] = this.ompai
        objetoParaEnvio['cor'] = this.cor
        if (this.editedIndex > -1) {
          // manda para edição
          this.$http.put('oms/' + this.id, objetoParaEnvio)
            .then(response => {
              Object.assign(this.oms[this.editedIndex], response.data)
              this.$toastr.s(
                'Om alterada com sucesso', 'Sucesso!'
              )
              this.close()
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível alterar a Om', 'Erro!'
              )
            })
        } else {
          // manda para criação
          this.$http.post('oms', objetoParaEnvio)
            .then(response => {
              this.oms.push(response.data)
              this.$toastr.s(
                'Om criada com sucesso', 'Sucesso!'
              )
              this.resetFields()
              this.close()
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível criar a Om', 'Erro!'
              )
            })
        }
        this.$http.get('oms/lepais')
          .then(response => {
            this.$emit('resetarPais', response.data)
            this.dataChart = {
              id: 1,
              title: 'Selecione uma opção',
              name: 'Selecione uma opção',
              children: []
            }
          })
          .catch(erro => console.log(erro))
      }
    },

    showOrgOm (id) {
      this.$http.get('oms/chart/' + id)
        .then(response => {
          this.dataChart = response.data
        })
        .catch(erro => console.log(erro))
    },

    closeChart () {
      this.dataChart = {
        id: 1,
        title: 'Selecione uma opção',
        name: 'Selecione uma opção',
        children: []
      }
    }
  }
}

</script>

<style>
  .btn-wrap {
    position: relative;
    text-align: center;
  }

  .btn-wrap button {
    position: absolute;
    z-index: 1;
    top: 10%;
    left: 6%;
    padding: 0;
    margin: -15px 0 0 -30px; /* margem à esquerda e ao topo metade da medida do botão */
    border: 0 none;
  }
</style>



