<template>

  <v-main>

    <v-container class="pt-5">

      <!--Banner-->
      <v-banner
        class="p-5"
        elevation="21">

        <v-icon
          size="36"
          slot="icon">
          mdi-map-marker-multiple
        </v-icon>

        <h2>Gerenciamento de SVP / SVP Gu (UA Pagadora) </h2>

      </v-banner>

      <!--DataTable-->
      <v-data-table
        :headers="headers"
        :items="locais"
        :search="search"
        class="elevation-21 mt-4"
      >
        <template v-slot:top>

          <v-toolbar
            flat>

            <v-toolbar-title>Tabela de SVP / SVP Gu Cadastradas</v-toolbar-title>

            <v-divider
              class="mx-4"
              inset
              vertical></v-divider>

            <!--Dialog de cadastro e edição-->
            <v-dialog
              max-width="50%"
              persistent v-model="dialog">

              <!--novo cadastro-->
              <template v-slot:activator="{ on, attrs }">

                <v-btn
                  class="mb-2"
                  color="primary"
                  dark
                  v-bind="attrs"
                  v-on="on">

                  <v-icon class="mr-5" small>
                    mdi-message-plus-outline
                  </v-icon>

                  Nova SVP / SVP Gu

                </v-btn>

              </template>

              <!--Card de cadastro edição-->
              <v-form>

                <v-card>

                  <v-card-title>

                    <v-icon class="mr-4">
                      fab fa-fort-awesome-alt
                    </v-icon>
                    <span class="headline">
                       {{ formTitle }}</span>

                  </v-card-title>

                  <v-card-text>

                    <v-container>

                      <v-row>

                        <!--nome - sigla  -->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4">

                          <!--Nome-->
                          <v-row>

                            <v-col>

                              <v-text-field
                                :error-messages="uapagadoraErrors"
                                @blur="$v.UA_PAGADORA.$touch()"
                                @input="$v.UA_PAGADORA.$touch()"
                                dense
                                hint="Por exemplo: Centro de Embarcações do Comando Militar da Amazônia"
                                label="Nome"
                                name="nome"
                                outlined
                                placeholder="Insira o Nome da SVP / SVP Gu"
                                required
                                v-model="UA_PAGADORA">

                              </v-text-field>

                            </v-col>

                          </v-row>

                        </v-col>
                        <!--COR-->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4">

                          <v-card
                            elevation="2">

                            <v-container fluid>

                              <v-row>

                                <v-col>
                                  <p><b>Cor de referencia da Om</b></p>
                                  <v-color-picker
                                    canvas-height="150"
                                    hide-inputs
                                    mode="hexa"
                                    name="cor"
                                    v-model="cor"
                                    width="500">
                                  </v-color-picker>

                                  <v-alert type="info">
                                    Apenas para gerenciamento de estatísticas.
                                  </v-alert>

                                </v-col>

                              </v-row>

                            </v-container>

                          </v-card>

                        </v-col>

                      </v-row>

                      <!--Botões-->
                      <v-row>

                        <v-col>

                          <!--Salvar-->
                          <v-btn
                            @click="save"
                            color="green darken-1">
                            Salvar
                          </v-btn>

                          <!--Cancelar-->
                          <v-btn
                            @click="close"
                            class="ml-6"
                            color="red darken-1">
                            Cancelar
                          </v-btn>

                        </v-col>

                      </v-row>

                    </v-container>

                  </v-card-text>

                </v-card>

              </v-form>

            </v-dialog>

            <v-spacer></v-spacer>

            <!--Pesquisar-->
            <v-text-field
              append-icon="mdi-magnify"
              hide-details
              label="Pesquisar"
              placeholder="Pesquisar"
              single-line
              v-model="search">
            </v-text-field>

            <!--Dialog para deletar Svp Svp Gu-->
            <v-dialog max-width="800px" v-model="dialogDelete">

              <v-card>

                <v-card-title class="justify-center" primary-title>

                  <v-icon class="mr-4">fa fa-exclamation-triangle</v-icon>
                  Você tem certeza que quer deletar esta SVP / SVP Gu
                  <v-icon class="ml-4">fa fa-exclamation-triangle</v-icon>

                </v-card-title>

                <v-card-text>
                  <v-row>
                    <v-col class="text-center">
                      <p>Essa ação é irreversível. Tenha certeza do que está fazendo.</p>
                    </v-col>
                  </v-row>
                </v-card-text>

                <v-card-actions class="pb-5">

                  <v-spacer></v-spacer>

                  <v-btn
                    @click="closeDelete"
                    color="grey lighten-1">
                    Cancelar
                  </v-btn>

                  <span class="pl-5 pr-5"></span>

                  <v-btn
                    @click="deleteSvpConfirm"
                    color="red lighten-1">
                    Excluir
                  </v-btn>

                </v-card-actions>

              </v-card>

            </v-dialog>

          </v-toolbar>

        </template>

        <!--Template de botões para editar e excluir -->
        <template v-slot:item.actions="{ item }">

          <!--editar-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="editSvp(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on">
                mdi-pencil
              </v-icon>
            </template>
            <span>Editar SVP / SVP Gu</span>
          </v-tooltip>

          <!--Excluir-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="deleteSvp(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on">
                mdi-delete
              </v-icon>
            </template>
            <span>Excluir SVP / SVP Gu</span>
          </v-tooltip>
        </template>

        <!--Ajuste de cores-->
        <template v-slot:item.cor="{ item }">
          <v-chip
            :color="getColor(item.cor)"
            dark>
          </v-chip>
        </template>

      </v-data-table>

    </v-container>

  </v-main>

</template>
<script>import {validationMixin} from 'vuelidate'
import {required} from 'vuelidate/lib/validators'

export default {
  components: {},
  mixins: [validationMixin],
  directives: {},
  validations: {
    UA_PAGADORA: {required}
  },

  data: () => ({
    // Itens de tabelas
    headers: [
      {
        text: 'Nome',
        align: 'start',
        value: 'UA_PAGADORA'
      },
      {
        text: 'Cor',
        value: 'cor',
        align: 'center'
      },
      {
        text: 'Actions',
        value: 'actions',
        align: 'center',
        sortable: false
      }
    ],
    search: '',
    // Para edição e cadastro
    dialog: false,
    editedIndex: -1,
    UA_PAGADORA: '',
    cor: '',
    // para deletar
    dialogDelete: false,
    defaultSvp: {
      UA_PAGADORA: '',
      cor: ''
    },
    editedSvp: {
      UA_PAGADORA: '',
      cor: ''
    }
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nova SVP / SVP Gu' : 'Editar SVP / SVP Gu'
    },
    uapagadoraErrors () {
      const errors = []
      if (!this.$v.UA_PAGADORA.$dirty) return errors
      !this.$v.UA_PAGADORA.required && errors.push('O Campo "Nome" não pode ficar em branco! ')
      return errors
    },
    showColor () {
      if (typeof this.cor === 'string') return this.cor

      return JSON.stringify(Object.keys(this.cor).reduce((cor, key) => {
        cor[key] = Number(this.cor[key].toFixed(2))
        return cor
      }, {}), null, 2)
    }
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
    cor (value) {
      // temporary fix while there is no way to disable the alpha channel in the colorpicker component: https://github.com/vuetifyjs/vuetify/issues/9590
      if (value.toString().match(/#[a-zA-Z0-9]{8}/)) {
        this.cor = value.substr(0, 7)
      }
    }
  },

  props: {
    locais: Array
  },

  methods: {
    editSvp (item) {
      this.editedIndex = this.locais.indexOf(item)
      this.editedSvp = Object.assign({}, item)
      this.id = this.editedSvp.id
      this.UA_PAGADORA = this.editedSvp.UA_PAGADORA
      this.cor = this.editedSvp.cor
      this.$v.$touch()
      this.dialog = true
    },

    getColor (cor) {
      return cor
    },

    deleteSvp (item) {
      console.log(item)
      this.editedIndex = this.locais.indexOf(item)
      this.editedSvp = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteSvpConfirm () {
      this.$http.delete('ua/' + this.editedSvp.id)
      // eslint-disable-next-line no-return-assign
        .then(() => {
          this.locais.splice(this.editedIndex, 1)
          this.resetFields()
          this.$toastr.s(
            'SVP / SVP Gu removida com sucesso', 'Sucesso!'
          )
          this.closeDelete()
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover a SVP / SVP Gu', 'Erro!'
          )
        })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedSvp = Object.assign({}, this.defaultSvp)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    resetFields () {
      this.id = ''
      this.UA_PAGADORA = ''
      this.cor = ''
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedSvp = Object.assign({}, this.defaultSvp)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    save () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        let objetoParaEnvio = {}
        objetoParaEnvio['id'] = this.id
        objetoParaEnvio['UA_PAGADORA'] = this.UA_PAGADORA
        objetoParaEnvio['cor'] = this.cor
        if (this.editedIndex > -1) {
          // manda para edição
          this.$http.put('ua/' + this.id, objetoParaEnvio)
            .then(response => {
              Object.assign(this.locais[this.editedIndex], response.data)
              this.$toastr.s(
                'SVP / SVP Gu alterada com sucesso', 'Sucesso!'
              )
              this.close()
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível alterar a SVP / SVP Gu', 'Erro!'
              )
            })
        } else {
          // manda para criação
          this.$http.post('ua', objetoParaEnvio)
            .then(response => {
              this.locais.push(response.data)
              this.$toastr.s(
                'SVP / SVP Gu criada com sucesso', 'Sucesso!'
              )
              this.resetFields()
              this.close()
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível criar a SVP / SVP Gu', 'Erro!'
              )
            })
        }
      }
    }
  }
}

</script>

<style>
  .btn-wrap {
    position: relative;
    text-align: center;
  }

  .btn-wrap button {
    position: absolute;
    z-index: 1;
    top: 10%;
    left: 6%;
    padding: 0;
    margin: -15px 0 0 -30px; /* margem à esquerda e ao topo metade da medida do botão */
    border: 0 none;
  }
</style>



