<template>

  <div class="ml-5">
    <!--Home-->
    <v-tooltip bottom color="primary">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="ma-1"
          plain
          to="/" v-bind="attrs"
          v-if="usuarioResetado"
          v-on="on"
        >
          <v-icon center>
            mdi-home
          </v-icon>
        </v-btn>
      </template>
      <span>Página Inicial</span>
    </v-tooltip>

    <!--Administração do sistema-->
    <v-menu
      offset-y
      open-on-hover
      rounded="b-xl"
      v-if="usuarioResetado"
    >
      <template v-if="usuarioLogado.permissoes.indexOf('Administrador Geral') !== -1" v-slot:activator="{ on, attrs }">
        <v-btn
          color="white"
          plain
          v-bind="attrs"
          v-on="on"
        >
          <v-icon class="mr-5" small>fa fa-cogs</v-icon>
          Administração do Sistema
        </v-btn>
      </template>
      <!--list-->
      <v-list>
        <!--Gerenciar Oms-->
        <v-list-item to="/oms">
          <v-icon class="mr-5" color="black">mdi-home-map-marker</v-icon>
          <v-list-item-title>
            Gerenciar Oms
          </v-list-item-title>
        </v-list-item>
        <!--Gerenciar Secoes-->
        <v-list-item to="/secoes">
          <v-icon class="mr-5" color="black">fab fa-fort-awesome</v-icon>
          <v-list-item-title>
            Gerenciar Seções
          </v-list-item-title>
        </v-list-item>
        <!--Gerenciar Usuários-->
        <v-list-item to="/usuarios">
          <v-icon class="mr-5" color="black">fa fa-users</v-icon>
          <v-list-item-title>
            Gerenciar Usuários
          </v-list-item-title>
        </v-list-item>
        <!--Gerenciar SVP / SVP Gu-->
        <v-list-item to="/locais">
          <v-icon class="mr-5" color="black">mdi-map-marker-multiple</v-icon>
          <v-list-item-title>
            Gerenciar SVP / SVP Gu
          </v-list-item-title>
        </v-list-item>
        <!--Gerenciar Posto / Grad-->
        <v-list-item to="/postograd">
          <v-icon class="mr-5" color="black">mdi-star-circle</v-icon>
          <v-list-item-title>
            Gerenciar Posto/Grad
          </v-list-item-title>
        </v-list-item>
      </v-list>

    </v-menu>

    <!--Gerenciamento de Dados-->
    <v-menu
      offset-y
      open-on-hover
      rounded="b-xl"
      v-if="usuarioResetado"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          color="white"
          plain
          v-bind="attrs"
          v-on="on"
        >
          <v-icon class="mr-5" small>mdi-database</v-icon>
          Gerenciamento de Dados
        </v-btn>
      </template>
      <v-list>
        <!--Gerenciar decujos-->
        <v-list-item to="/decujos">
          <v-icon class="mr-5" color="black">mdi-christianity-outline</v-icon>
          <v-list-item-title>
            Gerenciar Decujus
          </v-list-item-title>
        </v-list-item>
        <!--Gerenciar de deps-->
        <v-list-item to="/dependentes">
          <v-icon class="mr-5" color="black">mdi-account-multiple-outline</v-icon>
          <v-list-item-title>
            Gerenciar Dependentes
          </v-list-item-title>
        </v-list-item>
        <!--Gerenciar de Processos-->
        <v-list-item to="/processos">
          <v-icon class="mr-5" color="black">mdi-file-document-box-multiple-outline</v-icon>
          <v-list-item-title>
            Gerenciar Processos
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>

    <!--teste-->
    <v-menu
      offset-y
      open-on-hover
      rounded="b-xl"
      v-if="usuarioResetado"
    >
      <template v-if="usuarioLogado.permissoes.indexOf('Delegado') !== -1 || usuarioLogado.permissoes.indexOf('Homologador') !== -1 || usuarioLogado.permissoes.indexOf('Executor') !== -1" v-slot:activator="{ on, attrs }">
        <v-btn
          color="white"
          plain
          to="/"
          v-bind="attrs"
          v-on="on"
        >
          <v-icon class="mr-5" small>mdi-clipboard-list-outline</v-icon>
          Relatórios
        </v-btn>
      </template>
    </v-menu>

  </div>
</template>

<script>import {logoutMixin} from '@/mixins'
import {mapGetters} from 'vuex'

export default {
  mixins: [logoutMixin],
  computed: {

    ...mapGetters(['usuarioResetado', 'usuarioLogado'])

  },
  data: () => ({
  }),
  mounted () {
  }
}
</script>



