<template>
  <v-main class="bgcammo">
    <v-container fluid>
      <v-form @submit.prevent="efetuarLogin">
        <v-container>
          <br>

          <v-row>
            <v-col cols="6"
                   offset="3">
              <v-card rounded>
                <v-row class="text-center">
                  <v-col cols="2"><img class="logo" src="../assets/img/doc3.png"></v-col>
                  <v-col cols="8"><h1>MORTO</h1>
                    <h3>Sistema de controle de "de cujus"</h3></v-col>
                  <v-col cols="2"></v-col>
                </v-row>
              </v-card>

            </v-col>
          </v-row>

          <v-row>
            <v-col
              cols="6"
              offset="3"
            >
              <v-card
                elevation="10"
                rounded="lg"
              >

                <v-card-title>
                  <v-icon class="mr-4">
                    fa fa-user
                  </v-icon>
                  Login
                </v-card-title>
                <v-container>

                  <!--cpf-->
                  <v-row>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        clearable
                        dense
                        id="cpf"
                        label="CPF"
                        name="cpf"
                        outlined
                        placeholder="Insira o seu CPF"
                        required
                        v-mask-cpf
                        v-model="usuario.cpf"
                      />
                    </v-col>
                  </v-row>

                  <!--Password-->
                  <v-row>
                    <v-col
                      cols="12"
                    >
                      <v-text-field
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        @click:append="show1 = !show1"
                        clearable
                        counter
                        dense
                        hint="No mínimo 6 caracteres"
                        label="Senha"
                        name="password"
                        outlined
                        required
                        v-model="usuario.password"
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <!--Submit o que é --- obter acesso-->
                  <v-row>
                    <!--logar-->
                    <v-col>

                      <v-btn
                        color="primary"
                        elevation="2"
                        type="submit"
                      >Logar
                      </v-btn>

                    </v-col>

                  </v-row>

                  <v-row>
                    <!--O que é? e obter acesso-->
                    <v-col class="text-right">
                      <v-btn block x-small
                        @click="openDialogAcesso"
                        color="secondary"
                        elevation="2"
                      >Como Obter Acesso?
                      </v-btn>
                    </v-col>
                    <v-col>

                      <v-btn block x-small
                        @click="openDialogWhat"
                        color="secondary"
                        elevation="2"
                      >O que é o Morto?
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-container>

              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-form>
    </v-container>

    <!--Dialog para ver detalhes sobre o sistema-->
    <v-dialog max-width="70%" v-model="dialogWhat">

      <v-card>

        <v-card-title class="justify-center" primary-title>

          O que é o SCC?

        </v-card-title>

        <v-card-text>
          <p>Em construção</p>
        </v-card-text>

        <v-card-actions class="pb-5">

          <v-spacer></v-spacer>

          <v-btn
            @click="dialogWhat = false"
            color="grey lighten-1">
            Fechar
          </v-btn>

        </v-card-actions>

      </v-card>

    </v-dialog>

    <!--Dialog para pedir acesso ao sistema-->
    <v-dialog max-width="40%" v-model="dialogAcesso">

      <v-card>

        <v-card-title class="justify-center" primary-title>

          Como obter acesso?

        </v-card-title>

        <v-card-text>
         <p>Atualmente o sistema encontra-se em teste (versão alpha) e esta funcionalidade está desabilitada.</p>
          <p>Para maiores informações, entre em contato com o Maj Brilhante, pelo email <a href="mailto:tiagobrilhantemania@gmail.com">tiagobrilhantemania@gmail.com</a></p>
        </v-card-text>

        <v-card-actions class="pb-5">

          <v-spacer></v-spacer>

          <v-btn
            @click="dialogAcesso = false"
            color="grey lighten-1">
            Fechar
          </v-btn>

        </v-card-actions>

      </v-card>

    </v-dialog>
  </v-main>
</template>

<script>import {cpf} from 'cpf-cnpj-validator'

export default {
  data () {
    return {
      usuario: {},
      show1: false,
      dialogWhat: false,
      dialogAcesso: false
    }
  },
  methods: {
    efetuarLogin () {
      if (!cpf.isValid(this.usuario.cpf)) {
        this.$toastr.e(
          'CPF invalido', 'Erro!'
        )
      } else {
        this.$store.dispatch('efetuarLogin', this.usuario)
          .then(response => {
            if (response.user.reset) {
              this.$router.push({name: 'reset'})
            } else {
              this.$router.push({name: 'home'})
            }
          })
          .catch(erro => {
            if (erro.request.status === 401) {
              this.$toastr.e(
                'Login ou senha inválidos', 'Erro!'
              )
            }
          })
      }
    },

    openDialogWhat () {
      this.$http.get('contawhat')
        .then(() => {
          this.dialogWhat = true
        }, err => {
          console.log(err)
        })
    },

    openDialogAcesso () {
      this.dialogAcesso = true
    }
  }
}
</script>
<style>
  .bgcammo {
    background-image: url('../assets/img/cammo.jpg');
    background-repeat: repeat;
    background-size: contain;
    background-color: #cccccc;
  }

  .logo {
    width: inherit;
    position: inherit;
  }
</style>
