<template>
  <v-main>
    <v-container class="pt-5">

      <!--Banner-->
      <v-banner
        class="p-5"
        elevation="21"
      >

        <v-icon
          size="36"
          slot="icon">
          fa fa-user
        </v-icon>

        <h2>Gerenciamento de Usuários </h2>

      </v-banner>

      <!--DataTable-->
      <v-data-table
        :headers="headers"
        :items="usuarios"
        :search="search"
        class="elevation-21 mt-4"
        sort-by="nome"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
            <v-toolbar-title>Tabela de Usuários Cadastrados</v-toolbar-title>

            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>

            <!--Dialog de cadastro e edição-->
            <v-dialog
              max-width="70%"
              v-model="dialog">

              <!--novo cadastro-->
              <template v-slot:activator="{ on, attrs }">

                <v-btn
                  class="mb-2"
                  color="primary"
                  dark
                  v-bind="attrs"
                  v-on="on"
                >
                  <v-icon class="mr-5" x-small>fa fa-user-plus</v-icon>
                  Novo Usuário
                </v-btn>

              </template>

              <!--Card de cadastro edição-->
              <v-form>

                <v-card>

                  <v-card-title>

                    <v-icon class="mr-4">
                      fa fa-user
                    </v-icon>
                    <span class="headline">
                       {{ formTitle }}</span>

                  </v-card-title>

                  <v-card-text>

                    <v-container>

                      <!--Nome - PostoGrad - NomeGuerra - CPF - seção e telefone-->
                      <v-row>

                        <!--Nome-->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4"
                        >
                          <v-text-field
                            :error-messages="nomeErrors"
                            @blur="$v.nome.$touch()"
                            @input="$v.nome.$touch()"
                            dense
                            label="Nome"
                            name="nome"
                            outlined
                            placeholder="Insira o nome completo"
                            required
                            v-model="nome"
                          ></v-text-field>
                        </v-col>

                        <!--Posto Grad-->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4"
                        >
                          <v-select
                            :error-messages="postoGradErrors"
                            :items="posto_grad_options"
                            @blur="$v.posto_grad.$touch()"
                            @input="$v.posto_grad.$touch()"
                            dense
                            label="Posto / Grad"
                            name="posto_grad"
                            outlined
                            placeholder="Insira o Posto/Grad"
                            required
                            v-model="posto_grad"
                          ></v-select>
                        </v-col>

                        <!--Nome de Guerra-->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4"
                        >
                          <v-text-field
                            :error-messages="nomeGuerraErrors"
                            @blur="$v.nome_guerra.$touch()"
                            @input="$v.nome_guerra.$touch()"
                            dense
                            label="Nome de Guerra"
                            name="nome_guerra"
                            outlined
                            placeholder="Insira o nome de guerra"
                            required
                            v-model="nome_guerra"
                          ></v-text-field>
                        </v-col>

                        <!--CPF-->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4"
                        >
                          <v-text-field
                            :error-messages="cpfErrors"
                            @blur="$v.cpf.$touch()"
                            dense
                            label="CPF"
                            name="cpf"
                            outlined
                            placeholder="Insira o CPF do usuário"
                            required
                            v-mask-cpf
                            v-model="cpf"
                          ></v-text-field>
                        </v-col>

                        <!--Seção-->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4"
                        >
                          <v-select
                            :error-messages="secaoErrors"
                            :items="secoes"
                            @blur="$v.secao.$touch()"
                            @input="$v.secao.$touch()"
                            dense
                            item-text="sigla"
                            item-value="id"
                            label="Seção"
                            name="secao"
                            outlined
                            placeholder="Selecione a Seção do usuário"
                            required
                            v-model="secao"
                          ></v-select>
                        </v-col>

                        <!--Telefone com WPP-->
                        <v-col>
                          <v-text-field
                            :error-messages="telErrors"
                            @blur="$v.tel.$touch()"
                            @input="$v.tel.$touch()"
                            dense
                            label="Telefone de contato"
                            name="tel"
                            outlined
                            placeholder="Telefone preferencialmente com Whatsapp"
                            required
                            v-mask-phone.br
                            v-model="tel"
                          ></v-text-field>
                        </v-col>

                      </v-row>

                    </v-container>

                  </v-card-text>

                  <!--Botões-->
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <!--Cancelar-->
                    <v-btn
                      @click="close"
                      color="red darken-1"
                    >
                      Cancelar
                    </v-btn>
                    <!--Salvar-->
                    <v-btn
                      :disabled="loadingButton"
                      :loading="loadingButton"
                      @click="save"
                      color="green darken-1"
                    >
                      Salvar
                    </v-btn>
                  </v-card-actions>

                </v-card>

              </v-form>

            </v-dialog>

            <v-spacer></v-spacer>

            <!--Pesquisar-->
            <v-text-field
              append-icon="mdi-magnify"
              hide-details
              label="Pesquisar"
              placeholder="Pesquisar"
              single-line
              v-model="search"
            ></v-text-field>

            <!--Dialog para deletar usuário-->
            <v-dialog max-width="800px" v-model="dialogDelete">
              <v-card>
                <v-card-title class="justify-center" primary-title>
                  <v-icon
                    class="mr-4">
                    fa fa-exclamation-triangle
                  </v-icon>
                  Você tem certeza que quer deletar o usuário {{ editedUser.posto_grad }} {{ editedUser.nome_guerra }}?
                  <v-icon
                    class="ml-4">
                    fa fa-exclamation-triangle
                  </v-icon>

                </v-card-title>
                <v-card-text>
                  <div class="text-center">Essa ação é irreversível. Tenha certeza do que está fazendo.</div>
                </v-card-text>
                <v-card-actions class="pb-5">
                  <v-spacer></v-spacer>
                  <v-btn @click="closeDelete" color="grey lighten-1">Cancelar</v-btn>
                  <span class="pl-5 pr-5"></span>
                  <v-btn @click="deleteUserConfirm" color="red lighten-1">Excluir</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <!--Dialog para resetar usuário-->
            <v-dialog max-width="800px" v-model="dialogReset">
              <v-card>

                <v-card-title class="justify-center" primary-title>

                  <v-icon
                    class="mr-4">
                    fa fa-exclamation-triangle
                  </v-icon>
                  Você tem certeza que quer resetar a senha do usuário {{ editedUser.posto_grad }} {{
                  editedUser.nome_guerra }}?
                  <v-icon
                    class="ml-4">
                    fa fa-exclamation-triangle
                  </v-icon>

                </v-card-title>

                <v-card-text>
                  <div class="text-center"><p>Isso fará com que a senha de acesso seja o CPF do usuário, sendo excluídos
                    os "." e "-".</p>
                    <p>Por exemplo: se o CPF do usuário for 876.985.410-61 , a senha resetada será 87698541061. </p>
                    <p>Avise isso ao usuário.</p>
                    <p>Quando um usuário resetado entra no sistema, é obrigatóra a mudança de senha.</p></div>
                </v-card-text>
                <v-card-actions class="pb-5">
                  <v-spacer></v-spacer>
                  <v-btn @click="closeReset" color="grey lighten-1">Cancelar</v-btn>
                  <span class="pl-5 pr-5"></span>
                  <v-btn @click="resetUserConfirm" color="red lighten-1">Resetar</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>

          </v-toolbar>

        </template>

        <!--Template de botões para editar, excluir e resetar senha-->
        <template v-slot:item.actions="{ item }">

          <!--editar-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="editUser(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on"
              >
                mdi-pencil
              </v-icon>
            </template>
            <span>Editar Usuário</span>
          </v-tooltip>

          <!--Excluir-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="deleteUser(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on"
              >
                mdi-delete
              </v-icon>
            </template>
            <span>Excluir Usuário</span>
          </v-tooltip>

          <!--Resetar-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="resetUser(item)"
                small
                v-bind="attrs"
                v-on="on"
              >
                mdi-lock
              </v-icon>
            </template>
            <span>Resetar senha de usuário</span>
          </v-tooltip>

        </template>

        <!--Ajuste de icones no campo reset-->
        <template v-slot:item.reset="{ item }">
          <v-chip
            :color="getColor(item.reset)"
            dark
          >
            <v-tooltip top>
              <template v-slot:activator="{ on, attrs }">
                <v-icon
                  center
                  v-bind="attrs"
                  v-on="on">
                  {{ getIcon(item.reset) }}
                </v-icon>
              </template>
              <span>{{ getTooltipResetIcon(item.reset) }}</span>
            </v-tooltip>
          </v-chip>
        </template>

        <!--Ajuste de icones no campo secao-->
        <template v-slot:item.secao="{ item }">

          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              {{ item.secao.sigla }}
              <v-icon
                @click="openDialogPermissao(item)"
                center
                class="ml-4"
                v-bind="attrs"
                v-on="on">
                mdi-magnify
              </v-icon>
            </template>
            <span>Informações sobre permissões</span>
          </v-tooltip>

        </template>

      </v-data-table>

      <!--Dialog para exibir permissões de uma seção - usuario-->
      <v-dialog max-width="800px" v-model="dialogExibePermissao">
        <v-card>

          <v-card-title primary-title>
            Permissões de acesso do usuário: {{ listaNomePerm }} / {{ listaSecaoPerm }}.
          </v-card-title>

          <v-card-text>
            <ul>
              <li :key="perm.id" v-for="perm in listaPermissoesUserPerm"> {{ perm.permissao }}</li>
            </ul>

            <v-alert class="mt-8" type="info">
              A lista de permissões de um usuário está diretamente vinculada a seção a qual ele pertence.
            </v-alert>
          </v-card-text>
          <v-card-actions class="pb-5">
            <v-spacer></v-spacer>
            <v-btn @click="dialogExibePermissao = false" color="grey lighten-1">Fechar</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </v-container>
  </v-main>
</template>
<script>import {cpf} from 'cpf-cnpj-validator'
import {validationMixin} from 'vuelidate'
import {required} from 'vuelidate/lib/validators'

let validaCpf = (value) => cpf.isValid(value)

export default {
  mixins: [validationMixin],
  validations: {
    nome: {required},
    nome_guerra: {required},
    posto_grad: {required},
    secao: {required},
    tel: {required},
    cpf: {
      required,
      validaCpf,
      async isUniqueCpf (value) {
        let objetoEnvio = {}
        objetoEnvio['id'] = this.id
        objetoEnvio['cpf'] = value
        let resposta = await this.$http.post('users/cpf', objetoEnvio)
          .then(response => {
            if (response.data === 0) {
              return true
            } else {
              return false
            }
          })
          .catch(erro => console.log(erro))
        return resposta
      }
    }
  },

  data: () => ({
    posto_grad_options: ['Gen Ex', 'Gen Div', 'Gen Bda', 'Cel', 'Ten Cel', 'Maj', 'Cap', '1º Ten', '2º Ten', 'Asp', 'STen', '1º Sgt', '2º Sgt', '3º Sgt', 'Cb', 'Sd', 'SC'],
    dialog: false,
    search: '',
    dialogDelete: false,
    dialogReset: false,
    dialogExibePermissao: false,
    listaNomePerm: '',
    listaSecaoPerm: '',
    listaPermissoesUserPerm: [],
    headers: [
      {
        text: 'Nome',
        align: 'start',
        value: 'nome'
      },
      {
        text: 'Nome de Guerra',
        value: 'guerra'
      },
      {
        text: 'CPF',
        value: 'cpf'
      },
      {
        text: 'Telefone',
        value: 'tel'
      },
      {
        text: 'Seção',
        value: 'secao',
        align: 'center'
      },
      {
        text: 'Om',
        value: 'secao.om.sigla',
        align: 'center'
      },
      {
        text: 'Reset',
        value: 'reset',
        align: 'center'
      },
      {
        text: 'Actions',
        value: 'actions',
        align: 'center',
        sortable: false
      }
    ],
    editedIndex: -1,
    id: '',
    nome: '',
    nome_guerra: '',
    guerra: '',
    tel: '',
    cpf: '',
    secao: '',
    posto_grad: '',
    loadingButton: false,
    defaultUser: {
      nome: '',
      nome_guerra: '',
      guerra: '',
      cpf: '',
      tel: '',
      secao: '',
      posto_grad: '',
      reseted: ''
    },
    editedUser: {
      id: '',
      nome: '',
      nome_guerra: '',
      guerra: '',
      tel: '',
      cpf: '',
      secao: '',
      posto_grad: '',
      reseted: ''
    }
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Novo Usuário' : 'Editar Usuário'
    },
    nomeErrors () {
      const errors = []
      if (!this.$v.nome.$dirty) return errors
      !this.$v.nome.required && errors.push('O Campo "Nome" não pode ficar em branco! ')
      return errors
    },
    nomeGuerraErrors () {
      const errors = []
      if (!this.$v.nome_guerra.$dirty) return errors
      !this.$v.nome_guerra.required && errors.push('O Campo "Nome de Guerra" não pode ficar em branco! ')
      return errors
    },
    postoGradErrors () {
      const errors = []
      if (!this.$v.posto_grad.$dirty) return errors
      !this.$v.posto_grad.required && errors.push('O Campo "Posto / Grad" não pode ficar em branco! ')
      return errors
    },
    telErrors () {
      const errors = []
      if (!this.$v.tel.$dirty) return errors
      !this.$v.tel.required && errors.push('O Campo "Telefone de contato" não pode ficar em branco! ')
      return errors
    },
    secaoErrors () {
      const errors = []
      if (!this.$v.secao.$dirty) return errors
      !this.$v.secao.required && errors.push('O Campo "Seção" deve apresentar uma opção válida! ')
      return errors
    },
    cpfErrors () {
      const errors = []
      if (!this.$v.cpf.$dirty) return errors
      !this.$v.cpf.required && errors.push('O Campo "CPF" não pode ficar em branco!')
      !this.$v.cpf.validaCpf && errors.push('O CPF informado não é valido!')
      !this.$v.cpf.isUniqueCpf && errors.push('O CPF informado já está em uso!')
      return errors
    }
  },

  watch: {
    dialog (val) {
      this.loadingButton = false
      this.$v.$reset()
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    }
  },

  props: {
    usuarios: Array,
    secoes: Array
  },

  methods: {
    editUser (item) {
      this.editedIndex = this.usuarios.indexOf(item)
      this.editedUser = Object.assign({}, item)
      this.id = this.editedUser.id
      this.nome = this.editedUser.nome
      this.nome_guerra = this.editedUser.nome_guerra
      this.posto_grad = this.editedUser.posto_grad
      this.tel = this.editedUser.tel
      this.cpf = this.editedUser.cpf
      this.secao = this.editedUser.secao.id
      this.dialog = true
    },

    getColor (reset) {
      if (reset > 0) {
        return 'red'
      } else {
        return 'green'
      }
    },

    getIcon (reset) {
      if (reset > 0) {
        return 'mdi-account-alert-outline'
      } else {
        return 'mdi-check'
      }
    },

    getTooltipResetIcon (reset) {
      if (reset > 0) {
        return 'Este usuário encontra-se com a senha resetada!'
      } else {
        return 'Situação normal'
      }
    },

    deleteUser (item) {
      this.editedIndex = this.usuarios.indexOf(item)
      this.editedUser = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteUserConfirm () {
      this.$http.delete('users/' + this.editedUser.id)
      // eslint-disable-next-line no-return-assign
        .then(() => {
          this.usuarios.splice(this.editedIndex, 1)
          this.closeDelete()
          this.$toastr.s(
            'Usuário removido com sucesso', 'Sucesso!'
          )
          this.resetFields()
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover o Usuário', 'Erro!'
          )
        })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedUser = Object.assign({}, this.defaultUser)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    resetUser (item) {
      this.editedIndex = this.usuarios.indexOf(item)
      this.editedUser = Object.assign({}, item)
      this.dialogReset = true
    },

    resetUserConfirm () {
      this.$http.post('users/reset/password', this.editedUser)
      // eslint-disable-next-line no-return-assign
        .then(response => {
          this.closeReset()
          this.$toastr.s(
            'Senha resetada com sucesso. A nova senha será o cpf do usuário, sem os caracteres . e -', 'Sucesso!'
          )
          Object.assign(this.usuarios[this.editedIndex], response.data)
          this.resetFields()
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível resetar a senha do Usuário', 'Erro!'
          )
        })
    },

    closeReset () {
      this.dialogReset = false
      this.$nextTick(() => {
        this.editedUser = Object.assign({}, this.defaultUser)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    resetFields () {
      this.id = ''
      this.nome = ''
      this.nome_guerra = ''
      this.guerra = ''
      this.cpf = ''
      this.tel = ''
      this.secao = ''
      this.posto_grad = ''
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedUser = Object.assign({}, this.defaultUser)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    openDialogPermissao (item) {
      this.dialogExibePermissao = true
      this.listaNomePerm = item.posto_grad + ' ' + item.nome_guerra
      this.listaSecaoPerm = item.secao.sigla
      this.listaPermissoesUserPerm = item.secao.permissoes
    },

    save () {
      this.loadingButton = true
      this.$v.$touch()
      setTimeout(() => {
        if (!this.$v.$invalid) {
          let objetoParaEnvio = {}

          objetoParaEnvio['id'] = this.id
          objetoParaEnvio['nome'] = this.nome
          objetoParaEnvio['nome_guerra'] = this.nome_guerra
          objetoParaEnvio['posto_grad'] = this.posto_grad
          objetoParaEnvio['cpf'] = this.cpf
          objetoParaEnvio['secao_id'] = this.secao
          objetoParaEnvio['tel'] = this.tel

          if (this.editedIndex > -1) {
            this.$http.put('users/' + objetoParaEnvio.id, objetoParaEnvio)
              .then(response => {
                Object.assign(this.editedIndex, response.data)
                this.$toastr.s(
                  'Usuário alterado com sucesso', 'Sucesso!'
                )
                this.loadingButton = false
              }, err => {
                console.log(err)
                this.$toastr.e(
                  'Não foi possível alterar o Usuário', 'Erro!'
                )
              })
          } else {
            this.$http.post('users/create', objetoParaEnvio)
              .then(response => {
                this.usuarios.push(response.data)
                this.$toastr.s(
                  'Usuário criado com sucesso', 'Sucesso!'
                )
              }, err => {
                console.log(err)
                this.$toastr.e(
                  'Não foi possível criar o Usuário', 'Erro!'
                )
              })
          }
          this.resetFields()
          this.close()
        } else {
          this.loadingButton = false
        }
      }, 1000)
    }
  }
}

</script>



