<template>

  <v-main>

    <v-container class="pt-5">

      <!--Banner-->
      <v-banner
        class="p-5"
        elevation="21">

        <v-icon
          size="36"
          slot="icon">
          mdi-star-circle
        </v-icon>

        <h2>Gerenciamento de Posto / Grad </h2>

      </v-banner>

      <!--DataTable-->
      <v-data-table
        :headers="headers"
        :items="pg"
        :items-per-page=50
        :search="search"
        class="elevation-21 mt-4"
      >
        <template v-slot:top>

          <v-toolbar
            flat>

            <v-toolbar-title>Tabela de Posto / Grad Cadastrados</v-toolbar-title>

            <v-divider
              class="mx-4"
              inset
              vertical></v-divider>

            <!--Dialog de cadastro e edição-->
            <v-dialog
              max-width="50%"
              persistent v-model="dialog">

              <!--novo cadastro-->
              <template v-slot:activator="{ on, attrs }">

                <v-btn
                  class="mb-2"
                  color="primary"
                  dark
                  v-bind="attrs"
                  v-on="on">

                  <v-icon class="mr-5" small>
                    mdi-message-plus-outline
                  </v-icon>

                  Novo Posto / Grad

                </v-btn>

              </template>

              <!--Card de cadastro edição-->
              <v-form>

                <v-card>

                  <v-card-title>

                    <v-icon class="mr-4">
                      mdi-star-circle
                    </v-icon>
                    <span class="headline">
                       {{ formTitle }}</span>

                  </v-card-title>

                  <v-card-text>

                    <v-container>

                      <!--hierarquia, descricao e cod posto-->
                      <v-row>

                        <!--descricao-->
                        <v-col>

                          <v-text-field
                            :error-messages="descricaoErrors"
                            @blur="$v.DESCRICAO.$touch()"
                            @input="$v.DESCRICAO.$touch()"
                            dense
                            hint="Por exemplo: Maj"
                            label="Descrição"
                            name="descricao"
                            outlined
                            placeholder="Insira a descrição do Posto / Grad"
                            required
                            v-model="DESCRICAO">

                          </v-text-field>

                        </v-col>

                        <!--hierarquia-->
                        <v-col>

                          <v-text-field
                            :error-messages="hierarquiaErrors"
                            @blur="$v.hierarquia.$touch()"
                            @input="$v.hierarquia.$touch()"
                            dense
                            hint="Por exemplo: 1"
                            label="Hierarquia"
                            name="hierarquia"
                            outlined
                            placeholder="Insira a hierarquia do Posto / Grad"
                            required
                            type="number"
                            v-model="hierarquia">

                          </v-text-field>

                        </v-col>

                        <!--cod posto-->
                        <v-col>

                          <v-text-field
                            :error-messages="codpostoErrors"
                            @blur="$v.COD_POSTO.$touch()"
                            @input="$v.COD_POSTO.$touch()"
                            dense
                            hint="Por exemplo: 22"
                            label="Código do Posto/Grad"
                            name="codposto"
                            outlined
                            placeholder="Insira o Código do Posto / Grad"
                            required
                            type="number"
                            v-model="COD_POSTO">

                          </v-text-field>

                        </v-col>

                      </v-row>

                      <!--Botões-->
                      <v-row>

                        <v-col>

                          <!--Salvar-->
                          <v-btn
                            @click="save"
                            color="green darken-1">
                            Salvar
                          </v-btn>

                          <!--Cancelar-->
                          <v-btn
                            @click="close"
                            class="ml-6"
                            color="red darken-1">
                            Cancelar
                          </v-btn>

                        </v-col>

                      </v-row>

                    </v-container>

                  </v-card-text>

                </v-card>

              </v-form>

            </v-dialog>

            <v-spacer></v-spacer>

            <!--Pesquisar-->
            <v-text-field
              append-icon="mdi-magnify"
              hide-details
              label="Pesquisar"
              placeholder="Pesquisar"
              single-line
              v-model="search">
            </v-text-field>

            <!--Dialog para deletar PostoGrad-->
            <v-dialog max-width="800px" v-model="dialogDelete">

              <v-card>

                <v-card-title class="justify-center" primary-title>

                  <v-icon class="mr-4">fa fa-exclamation-triangle</v-icon>
                  Você tem certeza que quer deletar este Posto / Grad
                  <v-icon class="ml-4">fa fa-exclamation-triangle</v-icon>

                </v-card-title>

                <v-card-text>
                  <v-row>
                    <v-col class="text-center">
                      <p>Essa ação é irreversível. Tenha certeza do que está fazendo.</p>
                    </v-col>
                  </v-row>
                </v-card-text>

                <v-card-actions class="pb-5">

                  <v-spacer></v-spacer>

                  <v-btn
                    @click="closeDelete"
                    color="grey lighten-1">
                    Cancelar
                  </v-btn>

                  <span class="pl-5 pr-5"></span>

                  <v-btn
                    @click="deletePGConfirm"
                    color="red lighten-1">
                    Excluir
                  </v-btn>

                </v-card-actions>

              </v-card>

            </v-dialog>

          </v-toolbar>

        </template>

        <!--Template de botões para editar e excluir -->
        <template v-slot:item.actions="{ item }">

          <!--editar-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="editPG(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on">
                mdi-pencil
              </v-icon>
            </template>
            <span>Editar Posto/Grad</span>
          </v-tooltip>

          <!--Excluir-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="deletePG(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on">
                mdi-delete
              </v-icon>
            </template>
            <span>Excluir Posto / Grad</span>
          </v-tooltip>
        </template>

      </v-data-table>

    </v-container>

  </v-main>

</template>
<script>import {validationMixin} from 'vuelidate'
import {required} from 'vuelidate/lib/validators'

export default {
  components: {},
  mixins: [validationMixin],
  directives: {},
  validations: {
    DESCRICAO: {required},
    hierarquia: {required},
    COD_POSTO: {required}
  },

  data: () => ({
    // Itens de tabelas
    headers: [
      {
        text: 'Hierarquia',
        align: 'start',
        value: 'hierarquia'
      },
      {
        text: 'Nome',
        align: 'start',
        value: 'DESCRICAO'
      },
      {
        text: 'Cod Posto',
        value: 'COD_POSTO',
        align: 'center'
      },
      {
        text: 'Actions',
        value: 'actions',
        align: 'center',
        sortable: false
      }
    ],
    search: '',
    // Para edição e cadastro
    dialog: false,
    editedIndex: -1,
    DESCRICAO: '',
    hierarquia: '',
    COD_POSTO: '',
    // para deletar
    dialogDelete: false,
    defaultPG: {
      DESCRICAO: '',
      hierarquia: '',
      COD_POSTO: ''
    },
    editedPG: {
      DESCRICAO: '',
      hierarquia: '',
      COD_POSTO: ''
    }
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Novo Posto / Grad' : 'Editar Posto / Grad'
    },
    descricaoErrors () {
      const errors = []
      if (!this.$v.DESCRICAO.$dirty) return errors
      !this.$v.DESCRICAO.required && errors.push('O Campo "Descrição" não pode ficar em branco! ')
      return errors
    },
    hierarquiaErrors () {
      const errors = []
      if (!this.$v.hierarquia.$dirty) return errors
      !this.$v.hierarquia.required && errors.push('O Campo "Hierarquia" não pode ficar em branco! ')
      return errors
    },
    codpostoErrors () {
      const errors = []
      if (!this.$v.COD_POSTO.$dirty) return errors
      !this.$v.COD_POSTO.required && errors.push('O Campo "Código do Posto/Grad" não pode ficar em branco! ')
      return errors
    }
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    }
  },

  props: {
    pg: Array
  },

  methods: {
    editPG (item) {
      this.editedIndex = this.pg.indexOf(item)
      this.editedPG = Object.assign({}, item)
      this.DESCRICAO = this.editedPG.DESCRICAO
      this.hierarquia = this.editedPG.hierarquia
      this.COD_POSTO = this.editedPG.COD_POSTO
      this.$v.$touch()
      this.dialog = true
    },

    deletePG (item) {
      this.editedIndex = this.pg.indexOf(item)
      this.editedPG = Object.assign({}, item)
      this.dialogDelete = true
    },

    deletePGConfirm () {
      this.$http.delete('postograd/' + this.editedPG.COD_POSTO)
      // eslint-disable-next-line no-return-assign
        .then(() => {
          this.pg.splice(this.editedIndex, 1)
          this.resetFields()
          this.$toastr.s(
            'Posto / Grad removido com sucesso', 'Sucesso!'
          )
          this.closeDelete()
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover o Posto / Grad', 'Erro!'
          )
        })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedPG = Object.assign({}, this.defaultPG)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    resetFields () {
      this.DESCRICAO = ''
      this.hierarquia = ''
      this.COD_POSTO = ''
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedPG = Object.assign({}, this.defaultPG)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    save () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        let arrayCod = []
        let arrayDesc = []
        let arrayHiera = []

        for (let i = 0; i < this.pg.length; i++) {
          arrayCod.push(this.pg[i].COD_POSTO)
          arrayDesc.push(this.pg[i].DESCRICAO)
          arrayHiera.push(this.pg[i].hierarquia)
        }
        let checaCod
        let checaDesc
        let checaHierar

        let objetoParaEnvio = {}
        objetoParaEnvio['DESCRICAO'] = this.DESCRICAO
        objetoParaEnvio['hierarquia'] = this.hierarquia
        objetoParaEnvio['COD_POSTO'] = this.COD_POSTO
        if (this.editedIndex > -1) {
          // nesse caso tenho que ver se existe alguma alteração no cod hierarquia e descrição do que já existe
          // nesse caso eu mudei o cod posto
          if (parseInt(this.editedPG.COD_POSTO) !== parseInt(this.COD_POSTO)) {
            checaCod = arrayCod.includes(parseInt(this.COD_POSTO))
          } else {
            checaCod = false
          }
          // nesse caso eu mudei a hierarquia
          if (parseInt(this.editedPG.hierarquia) !== parseInt(this.hierarquia)) {
            checaHierar = arrayHiera.includes(parseInt(this.hierarquia))
          } else {
            checaHierar = false
          }
          // nesse caso eu mudei a descricao
          if (this.editedPG.DESCRICAO !== this.DESCRICAO) {
            checaDesc = arrayDesc.includes(this.DESCRICAO)
          } else {
            checaDesc = false
          }
          // nesse caso, se alguma for verdadeira, não pode prosseguir
          let lemsgerro = ' '
          if (checaDesc || checaHierar || checaCod) {
            if (checaDesc) {
              lemsgerro = lemsgerro + ' <li>Já existe uma descrição igual a essa.</li>'
            }

            if (checaHierar) {
              lemsgerro = lemsgerro + ' <li>Já existe uma hierarquia igual a essa.</li>'
            }

            if (checaCod) {
              lemsgerro = lemsgerro + ' <li>Já existe um código igual a esse.</li>'
            }

            this.$toastr.e(
              'Revise os campos do formulário, existem dados já lançados! <br> <br><ul>' + lemsgerro + '</ul>', 'Erro!'
            )
          } else {
            this.$http.put('postograd/' + this.COD_POSTO, objetoParaEnvio)
              .then(response => {
                Object.assign(this.pg[this.editedIndex], response.data)
                this.$toastr.s(
                  'Posto / Grad alterado com sucesso', 'Sucesso!'
                )
                this.close()
              }, err => {
                console.log(err)
                this.$toastr.e(
                  'Não foi possível alterar o Posto / Grad', 'Erro!'
                )
              })
          }
        } else {
          // manda para criação
          // tenho que verificar se existem mesmos cod_posto, hierarquia ou descricao...
          if (arrayCod.includes(parseInt(this.COD_POSTO)) || arrayDesc.includes(this.DESCRICAO) || arrayHiera.includes(parseInt(this.hierarquia))) {
            let msgErrorTeste = ' '

            if (arrayCod.includes(parseInt(this.COD_POSTO))) {
              msgErrorTeste = msgErrorTeste + '<li>Codigo de posto repetido. </li>'
            }

            if (arrayDesc.includes(this.DESCRICAO)) {
              msgErrorTeste = msgErrorTeste + '<li>Descrição repetida. </li>'
            }
            if (arrayHiera.includes(parseInt(this.hierarquia))) {
              msgErrorTeste = msgErrorTeste + '<li>Hierarquia repetida. </li>'
            }

            this.$toastr.e(
              'Revise os campos do formulário, existem dados já lançados! <br> <br><ul>' + msgErrorTeste + '</ul>', 'Erro!'
            )
          } else {
            this.$http.post('postograd', objetoParaEnvio)
              .then(response => {
                this.pg.push(response.data)
                this.$toastr.s(
                  'Posto / Grad criado com sucesso', 'Sucesso!'
                )
                this.resetFields()
                this.close()
              }, err => {
                console.log(err)
                this.$toastr.e(
                  'Não foi possível criar o Posto / Grad', 'Erro!'
                )
              })
          }
        }
      }
    }
  }
}

</script>

<style>
  .btn-wrap {
    position: relative;
    text-align: center;
  }

  .btn-wrap button {
    position: absolute;
    z-index: 1;
    top: 10%;
    left: 6%;
    padding: 0;
    margin: -15px 0 0 -30px; /* margem à esquerda e ao topo metade da medida do botão */
    border: 0 none;
  }
</style>



