<template>
  <Om :oms="oms" :dataBtnPai="dataBtnPai"  @resetarPais="dataBtnPai = $event"/>
</template>

<script>import {mapGetters} from 'vuex'
import Om from '../components/Om'

export default {
  components: {
    Om
  },
  data () {
    return {
      oms: [],
      dataBtnPai: []
    }
  },
  mounted () {
    this.$http.get('oms')
      .then(response => {
        this.oms = response.data
      })
      .catch(erro => console.log(erro))

    this.$http.get('oms/lepais')
      .then(response => {
        this.dataBtnPai = response.data
      })
      .catch(erro => console.log(erro))
  },
  computed: {

    ...mapGetters(['usuarioLogado'])

  }
}
</script>



