          </span>

        </template>

        <!--Template para exibir Sit Mil-->
        <template v-slot:item.SITUAÇÃO_MILITAR="{ item }">

          <span class="yellow" v-if="item.SITUAÇÃO_MILITAR === null"> Não Informada</span>
