              </v-icon>
            </template>
            <span>Editar "de cujo"</span>
          </v-tooltip>

          <!--Excluir-->
          <v-tooltip top v-if="usuarioLogado.permissoes.indexOf('Administrador') !== -1 || usuarioLogado.permissoes.indexOf('Administrador Geral') !== -1">
            <template v-slot:activator="{ on, attrs }">
