  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Novo "de cujus"' : 'Editar "de cujus"'
    },
    NOMEErrors () {
      const errors = []
      if (!this.$v.NOME.$dirty) return errors
      !this.$v.NOME.required && errors.push('O Campo "Nome" não pode ficar em branco! ')
      return errors
    },
    localFalecimentoErrors () {
      const errors = []
      if (!this.$v.Local_Falecimento.$dirty) return errors
      !this.$v.Local_Falecimento.required && errors.push('O Campo "Local de Falecimento" não pode ficar em branco! ')
      return errors
    },
    PRECErrors () {
      const errors = []
      if (!this.$v.PREC.$dirty) return errors
      !this.$v.PREC.required && errors.push('O Campo "Prec" não pode ficar em branco! ')
      return errors
    },
    dataFalErrors () {
      const errors = []
      if (!this.$v.DATA_FAL.$dirty) return errors
      !this.$v.DATA_FAL.required && errors.push('O Campo "Data de Faleciemnto" não pode ficar em branco! ')
      return errors
    },
    pensaoDeErrors () {
      const errors = []
      if (!this.$v.Pensão_de.$dirty) return errors
      !this.$v.Pensão_de.required && errors.push('O Campo "Pensão de:" deve apresentar uma opção válida! ')
      return errors
    },
    dataInclusaoErrors () {
      const errors = []
      if (!this.$v.DATA_INCLUSAO.$dirty) return errors
      !this.$v.DATA_INCLUSAO.required && errors.push('O Campo "Data de Inclusão" não pode ficar em branco! ')
      return errors
    },
    sitMilErrors () {
      const errors = []
      if (!this.$v.SITUAÇÃO_MILITAR.$dirty) return errors
      !this.$v.SITUAÇÃO_MILITAR.required && errors.push('O Campo "Situação Militar" deve apresentar uma opção válida! ')
      return errors
    },
    omTroncoErrors () {
      const errors = []
      if (!this.$v.om_tronco.$dirty) return errors
      !this.$v.om_tronco.required && errors.push('O Campo "Om Tronco" deve apresentar uma opção válida! ')
      return errors
    },
    sitErrors () {
      const errors = []
      if (!this.$v.Situação.$dirty) return errors
      !this.$v.Situação.required && errors.push('O Campo "Situação" deve apresentar uma opção válida! ')
      return errors
    },
    CPErrors () {
      const errors = []
      if (!this.$v.CP.$dirty) return errors
      !this.$v.CP.required && errors.push('O Campo "CP" não pode ficar em branco! ')
      return errors
    },
    IdtErrors () {
