  },

  watch: {
    dialog (val) {
      this.loadingButton = false
      this.$v.$reset()
      if (this.editedIndex === -1) {
        this.diableDataEditDeCujo = false
      } else {
        this.diableDataEditDeCujo = true
        this.btnTxtDataEditDeCujo = 'Destravar Campos'
        this.btnIcoDataEditDeCujo = 'mdi-lock-open'
      }
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
    dateFal (val) {
      this.DATA_FAL = this.formatDate(this.dateFal)
    },
    dateInclu (val) {
      this.DATA_INCLUSAO = this.formatDate(this.dateInclu)
    },
    dateNasci (val) {
