    await this.getSitMil()
    await this.getSit()
    await this.getTcuStatus()
    await this.getParentesco()
    await this.getOmsTronco()
    await this.getUa()
    this.loading = true
  },

  methods: {
    // asyncs para a página
    async getInativos () {
      try {
        this.$http.get('inativo?page=' + this.inativos.current_page)
          .then(response => {
            this.loadTable = false
            this.inativos = response.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getPostoGrad () {
      try {
        this.$http.get('postograd')
          .then(response => {
            this.posto_grad_opt = response.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getSitMil () {
      try {
        this.$http.get('sitmil')
          .then(response => {
            this.sitmilopt = response.data.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getOmsTronco () {
      try {
        this.$http.get('oms')
          .then(response => {
            this.omsTronco = response.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getSit () {
      try {
        this.$http.get('sit')
          .then(response => {
            this.sitopt = response.data.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getParentesco () {
      try {
        this.$http.get('parent')
          .then(response => {
            this.parentopt = response.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getUa () {
      try {
        this.$http.get('ua')
          .then(response => {
            this.uaopt = response.data.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getTcuStatus () {
      try {
        this.$http.get('tcustatus')
          .then(response => {
            this.tcuopt = response.data.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },
    // fim dos async
    onPageChange () {
      this.getInativos()
    },
    // decujo
    showdecujo (item) {
      this.editedIndex = this.inativos.data.indexOf(item)
      this.editedDeCujus = Object.assign({}, item)
      this.dialogShowDeCujus = true
    },

    editDeCujus (item) {
      this.editedIndex = this.inativos.data.indexOf(item)
      this.editedDeCujus = Object.assign({}, item)
      this.id = this.editedDeCujus.id
      this.PREC = this.editedDeCujus.PREC
      this.CP = this.editedDeCujus.CP
      this.PROCESSO_N = this.editedDeCujus.PROCESSO_N
      this.COD_POSTO = this.editedDeCujus.COD_POSTO
      this.om_tronco = this.editedDeCujus.om_tronco
      this.SITUAÇÃO_MILITAR = this.editedDeCujus.SITUAÇÃO_MILITAR
      this.IDENT = this.editedDeCujus.IDENT
      this.CPF = this.editedDeCujus.CPF
      this.NOME = this.editedDeCujus.NOME
      this.DATA_FAL = this.editedDeCujus.DATA_FAL
      this.Local_Falecimento = this.editedDeCujus.Local_Falecimento
      this.Situação = this.editedDeCujus.Situação
      this.CAIXA = this.editedDeCujus.CAIXA
      this.EXIGENCIA = this.editedDeCujus.EXIGENCIA
      this.DATA_INCLUSAO = this.formataDataHora(this.editedDeCujus.DATA_INCLUSAO, 'Não')
      this.TCU = this.editedDeCujus.TCU
      this.DataJulgamento = this.editedDeCujus.DataJulgamento
      this.CP_Reserva = this.editedDeCujus.CP_Reserva
      this.Restabelecimento = this.editedDeCujus.Restabelecimento
      this.Terminal = this.editedDeCujus.Terminal
      this.Ato_Assecuratorio = this.editedDeCujus.Ato_Assecuratorio
      this.Promocao_post_mortem = this.editedDeCujus.Promocao_post_mortem
      this.Pensão_de = this.editedDeCujus.Pensão_de
      this.RGPS = this.editedDeCujus.RGPS
      this.deps = this.editedDeCujus.deps
      this.obs = this.editedDeCujus.obs
      this.dialog = true
    },

    deleteDeCujo (item) {
      this.editedIndex = this.inativos.data.indexOf(item)
      this.editedDeCujus = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteDeCujusConfirm () {
      this.$http.delete('inativo/' + this.editedDeCujus.id)
      // eslint-disable-next-line no-return-assign
        .then(response => {
          this.inativos.data.splice(this.editedIndex, 1)
          this.closeDelete()
          this.$toastr.s(
            '"de cujus" removido com sucesso', 'Sucesso!'
          )
          this.resetFields()
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover o "de cujus"', 'Erro!'
          )
        })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedDeCujus = Object.assign({}, this.defaultDeCujus)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    save () {
      this.loadingButton = true
      this.$v.$touch()
      setTimeout(() => {
        if (!this.$v.$invalid) {
          let objetoParaEnvio = {}
          objetoParaEnvio['PREC'] = this.PREC
          objetoParaEnvio['CP'] = this.CP
          objetoParaEnvio['PROCESSO_N'] = this.PROCESSO_N
          objetoParaEnvio['COD_POSTO'] = this.COD_POSTO
          objetoParaEnvio['SITUAÇÃO_MILITAR'] = this.SITUAÇÃO_MILITAR
          objetoParaEnvio['IDENT'] = this.IDENT
          objetoParaEnvio['CPF'] = this.CPF
          objetoParaEnvio['NOME'] = this.NOME
          objetoParaEnvio['DATA_FAL'] = this.DATA_FAL
          objetoParaEnvio['DATA_INCLUSAO'] = this.DATA_INCLUSAO
          objetoParaEnvio['om_tronco'] = this.om_tronco
          objetoParaEnvio['Local_Falecimento'] = this.Local_Falecimento
          objetoParaEnvio['Situação'] = this.Situação
          objetoParaEnvio['CAIXA'] = this.CAIXA
          objetoParaEnvio['EXIGENCIA'] = this.EXIGENCIA
          objetoParaEnvio['TCU'] = this.TCU
          objetoParaEnvio['DataJulgamento'] = this.DataJulgamento
          objetoParaEnvio['CP_Reserva'] = this.CP_Reserva
          objetoParaEnvio['Restabelecimento'] = this.Restabelecimento
          objetoParaEnvio['Terminal'] = this.Terminal
          objetoParaEnvio['Ato_Assecuratorio'] = this.Ato_Assecuratorio
          objetoParaEnvio['Promocao_post_mortem'] = this.Promocao_post_mortem
          objetoParaEnvio['Pensão_de'] = this.Pensão_de
          objetoParaEnvio['RGPS'] = this.RGPS
          // informações de dependentes
          objetoParaEnvio['deps'] = this.lenewdepnewdecujo
          // observações enviadas do decujus
          objetoParaEnvio['obs'] = this.lenewobsnewdecujo
          if (this.editedIndex > -1) {
            objetoParaEnvio['id'] = this.id
            this.$http.put('inativo/' + objetoParaEnvio.id, objetoParaEnvio)
              .then(response => {
                for (let i = 0; i < this.inativos.data.length; i++) {
                  if (this.inativos.data[i].id === response.data.id) {
                    this.inativos.data.splice(i, 1, response.data)
                  }
                }
                this.$toastr.s(
                  '"de cujus" alterado com sucesso', 'Sucesso!'
                )
                this.loadingButton = false
              }, err => {
                console.log(err)
                this.$toastr.e(
                  'Não foi possível alterar o "de cujus"', 'Erro!'
                )
              })
          } else {
            this.$http.post('inativo', objetoParaEnvio)
              .then(response => {
                this.getInativos()
                this.$toastr.s(
