          objetoDependente['Julgado_TCU'] = this.Julgado_TCU
          objetoDependente['Idt'] = this.Idt_dep
          objetoDependente['CPF'] = this.CPF_dep
          objetoDependente['DATANASC'] = this.DATANASC
          objetoDependente['COD_PARENTE'] = this.COD_PARENTE
          objetoDependente['COTA_PARTE'] = this.COTA_PARTE
          objetoDependente['COD_POSTO'] = this.COD_POSTO_DEP
          objetoDependente['UA_PAGADORA'] = this.UA_PAGADORA
          objetoDependente['INICIO_BENEF'] = this.INICIO_BENEF
          objetoDependente['FIM_BENEF'] = this.FIM_BENEF
          objetoDependente['ALT_BENEF'] = this.ALT_BENEF
          objetoDependente['obs'] = this.obsnewdep
          objetoDependente['INSTITUIDOR'] = this.editedDeCujus

          this.$http.put('dep/altera/' + this.id_dep, objetoDependente)
            .then(response => {
              for (let i = 0; i < this.editedDeCujus.deps.length; i++) {
                if (this.editedDeCujus.deps[i].id === this.id_dep) {
                  this.editedDeCujus.deps.splice(i, 1, response.data)
                }
              }
              this.resetFieldsdDep()
              this.dialogEditDep = false
              this.$toastr.s(
                'Dependente editado com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível editar o dependente!', 'Erro!'
              )
            })
        }
      } else if (this.deondeVemDep === 'JanelaNovoDeCujus') {
        if (contaErros !== 0) {
          this.$toastr.e(
            'Não foi possível inserir o dependente, revise os campos do formulário!', 'Erro!'
          )
        } else {
          // manda para o array
          let objetoDependente = {}

          objetoDependente['id'] = this.id_dep
          objetoDependente['NOME_DEP'] = this.NOME_DEP
          objetoDependente['PROCESSO_N'] = this.prec_cp_dep
          objetoDependente['Julgado_TCU'] = this.Julgado_TCU
          objetoDependente['Idt'] = this.Idt_dep
          objetoDependente['CPF'] = this.CPF_dep
          objetoDependente['DATANASC'] = this.DATANASC
          objetoDependente['COD_PARENTE'] = this.COD_PARENTE
          objetoDependente['COTA_PARTE'] = this.COTA_PARTE
          objetoDependente['COD_POSTO'] = this.COD_POSTO_DEP
          objetoDependente['UA_PAGADORA'] = this.UA_PAGADORA
          objetoDependente['INICIO_BENEF'] = this.INICIO_BENEF
          objetoDependente['FIM_BENEF'] = this.FIM_BENEF
          objetoDependente['ALT_BENEF'] = this.ALT_BENEF
          objetoDependente['obs'] = this.obsnewdep
          for (let i = 0; i < this.lenewdepnewdecujo.length; i++) {
            if (this.lenewdepnewdecujo[i].id === this.id_dep) {
              this.lenewdepnewdecujo.splice(i, 1, objetoDependente)
            }
          }
          this.resetFieldsdDep()
          this.dialogEditDep = false
        }
      }
    },

    resetFields () {
      this.PREC = ''
      this.CP = ''
      this.PROCESSO_N = ''
      this.COD_POSTO = ''
      this.SITUAÇÃO_MILITAR = ''
      this.IDENT = ''
      this.CPF = ''
      this.NOME = ''
      this.DATA_FAL = ''
      this.Local_Falecimento = ''
      this.Situação = ''
      this.om_tronco = {}
      this.CAIXA = ''
      this.EXIGENCIA = ''
      this.DATA_INCLUSAO = ''
      this.TCU = ''
      this.DataJulgamento = ''
      this.CP_Reserva = ''
      this.Terminal = ''
      this.Ato_Assecuratorio = ''
      this.Promocao_post_mortem = ''
      this.Pensão_de = ''
      this.Restabelecimento = ''
      this.pens_de = ''
      this.RGPS = ''
      this.deps = []
      this.obs = []
      this.andamento_processo = []
    },

    resetFieldsdDep () {
      this.NOME_DEP = ''
      this.COD_PARENTE = ''
      this.Idt_dep = ''
      this.CPF_dep = ''
      this.prec_cp_dep = ''
      this.COTA_PARTE = ''
      this.COD_POSTO_DEP = ''
      this.UA_PAGADORA = ''
      this.Julgado_TCU = ''
      this.INICIO_BENEF = ''
      this.dateIniBen = ''
      this.ALT_BENEF = ''
      this.dateAltBen = ''
      this.FIM_BENEF = ''
      this.dateFimBen = ''
      this.obsnewdep = []
      // list errors msg dep
      this.ErrorParentesco = ''
      this.idtDepError = ''
      this.ErrorCpf = ''
      this.ErrorNomeDep = ''
      this.postograddeperror = ''
      this.datanascerror = ''
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedDeCujus = Object.assign({}, this.defaultDeCujus)
        this.editedIndex = -1
        this.contadorIndexAux = 0
        this.contadorIndexAuxDep = 0
        this.contadorIndexAuxObsDep = 0
        this.lenewobsnewdecujo = []
        this.lenewdepnewdecujo = []
        this.resetFields()
      })
    },

    // utilitários

    formataDataHora (dataHora, ch) {
      if (dataHora === null) {
        return null
      } else if (dataHora[2] === '/') {
        return dataHora
      } else {
        let d = moment.tz(dataHora, 'America/Manaus')
        let dh
        if (ch === 'Sim') {
          dh = d.format('DD/MM/YYYY ( HH:mm:ss )')
        } else {
          dh = d.format('DD/MM/YYYY')
        }
        return dh
      }
    },

    contaTempo (inicio, fim) {
      let dini = moment.tz(inicio, 'America/Manaus')
      let dfim = moment.tz(fim, 'America/Manaus')
      var diff = moment(dfim, 'YYYY-MM-DD HH:mm:ss').diff(moment(dini, 'YYYY-MM-DD HH:mm:ss'))
      var horas = Math.round(moment.duration(diff).asDays())
      return horas
    },

    formatadorDeCPF (cpf) {
      if (validaCpf(cpf)) {
        // retorna cpf formatado
        return ['Ok', cpf[0] + cpf[1] + cpf[2] + '.' + cpf[3] + cpf[4] + cpf[5] + '.' + cpf[6] + cpf[7] + cpf[8] + '-' + cpf[9] + cpf[10]]
      } else {
        // retorna o erro
        let ajustes
        if (cpf.length === 11) {
          ajustes = cpf[0] + cpf[1] + cpf[2] + '.' + cpf[3] + cpf[4] + cpf[5] + '.' + cpf[6] + cpf[7] + cpf[8] + '-' + cpf[9] + cpf[10]
        } else {
          ajustes = cpf
        }
        return ['Erro', 'CPF Inválido (' + ajustes + ')']
      }
    },

    verificadorCpfDep (cpf) {
      if (validaCpf(cpf)) {
        let objetoEnvio = {}
        objetoEnvio['CPF'] = cpf
        let resposta = this.$http.post('dep/cpf', objetoEnvio)
          .then(response => {
            if (response.data === 0) {
              this.ErrorCpf = ''
            } else {
              this.ErrorCpf = 'Este CPF já se encontra cadastrado em nossa base de dados de dependentes!'
            }
          })
          .catch(erro => console.log(erro))
        this.ErrorCpf = ''
        return resposta
      } else {
        this.ErrorCpf = 'Este CPF é inválido!'
      }
    },

    formatDate (date) {
      if (!date) return null
      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },

    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },

    async searchFieldsIni () {
      await this.searchFields()
    },

    searchFields () {
      let objetoBusca = {}
      if (this.search.length < 3) {
        this.$toastr.e(
          'Digite ao menos 3 caracteres para iniciar a busca!', 'Erro!'
        )
      } else {
        objetoBusca['campo'] = this.search
        this.loadTable = true
        this.inativos = []
        try {
          this.$http.post('inativo/busca', objetoBusca)
            .then(response => {
              this.inativos = response.data
              this.loadTable = false
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível obter informações!', 'Erro!'
              )
            })
        } catch (e) {
          console.log(e)
        }
      }
    },

    limpaBusca () {
      this.search = ''
      this.getInativos()
    }
  }
}

</script>

<style>
  .v-messages__message {
    color: red;
  }
</style>



