      const errors = []
      if (!this.$v.NOME_DEP.$dirty) return errors
      !this.$v.NOME_DEP.required && errors.push('O Campo "Nome do Dependente" não pode ficar em branco! ')
      return errors
    },
    IdtDepErrors () {
      const errors = []
      if (!this.$v.Idt_dep.$dirty) return errors
      !this.$v.Idt_dep.required && errors.push('O Campo "Identidade do Dependente" não pode ficar em branco! ')
      return errors
    },
    CodPostoDepErrors () {
      const errors = []
      if (!this.$v.COD_POSTO_DEP.$dirty) return errors
      !this.$v.COD_POSTO_DEP.required && errors.push('O Campo "Posto / Grad" deve apresentar uma opção válida! ')
      return errors
    },
    CodParenteErrors () {
      const errors = []
      if (!this.$v.COD_PARENTE.$dirty) return errors
      !this.$v.COD_PARENTE.required && errors.push('O Campo "Parentesco" deve apresentar uma opção válida! ')
      return errors
    },
    UaPagadoraErrors () {
      const errors = []
      if (!this.$v.UA_PAGADORA.$dirty) return errors
      !this.$v.UA_PAGADORA.required && errors.push('O Campo "UA Pagadora" deve apresentar uma opção válida! ')
      return errors
    },
    computedDateFormatted () {
      return this.formatDate(this.dateFal)
    },
    ...mapGetters(['usuarioLogado'])
  },

  watch: {
    dialog (val) {
      this.$v.$reset()
      if (this.editedIndex === -1) {
        this.resetFieldsdDep()
        this.editedDep = Object.assign({}, this.defaultDep)
        this.diableDataEditDep = false
        this.disableButton = true
      } else {
        this.diableDataEditDep = true
        this.btnTxtDataEditDep = 'Destravar Campos'
        this.btnIcoDataEditDep = 'mdi-lock-open'
      }
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
    dateNasci (val) {
      this.DATANASC = this.formatDate(this.dateNasci)
    },
    dateIniBen (val) {
      this.INICIO_BENEF = this.formatDate(this.dateIniBen)
    },
    dateAltBen (val) {
      this.ALT_BENEF = this.formatDate(this.dateAltBen)
    },
    dateFimBen (val) {
      this.FIM_BENEF = this.formatDate(this.dateFimBen)
    }
  },

  async mounted () {
    await this.getDependentes()
    await this.getPostoGrad()
    await this.getSitMil()
    await this.getSit()
    await this.getTcuStatus()
    await this.getParentesco()
    await this.getUa()
    this.loading = true
  },

  methods: {
    // asyncs para a página
    async getDependentes () {
      try {
        this.$http.get('dep?page=' + this.dependentes.current_page)
          .then(response => {
            this.loadTable = false
            this.dependentes = response.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getPostoGrad () {
      try {
        this.$http.get('postograd')
          .then(response => {
            this.posto_grad_opt = response.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getSitMil () {
      try {
        this.$http.get('sitmil')
          .then(response => {
            this.sitmilopt = response.data.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getSit () {
      try {
        this.$http.get('sit')
          .then(response => {
            this.sitopt = response.data.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getParentesco () {
      try {
        this.$http.get('parent')
          .then(response => {
            this.parentopt = response.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getUa () {
      try {
        this.$http.get('ua')
          .then(response => {
            this.uaopt = response.data.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    async getTcuStatus () {
      try {
        this.$http.get('tcustatus')
          .then(response => {
            this.tcuopt = response.data.data
          })
          .catch(erro => console.log(erro))
      } catch (e) {
        console.log(e)
      }
    },

    // fim dos async
    onPageChange () {
      this.getDependentes()
    },
    // dependente
    showdep (item) {
      this.editedIndex = this.dependentes.data.indexOf(item)
      this.editedDep = Object.assign({}, item)
      this.dialogShowDep = true
      this.exibeInfoProcShow = false
      this.exibeInfoInstituidor = false
    },

    deleteDeCujusConfirm () {
      this.$http.delete('inativo/' + this.editedDeCujus.id)
      // eslint-disable-next-line no-return-assign
        .then(response => {
          this.dependentes.data.splice(this.editedIndex, 1)
          this.closeDelete()
          this.$toastr.s(
            '"de cujus" removido com sucesso', 'Sucesso!'
          )
          this.resetFieldsdDep()
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover o "de cujus"', 'Erro!'
          )
        })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        // this.editedDeCujus = Object.assign({}, this.defaultDeCujus)
        this.editedIndex = -1
        this.resetFieldsdDep()
      })
    },

    save () {
      this.loadingButton = true
      this.disableButton = true
      this.$v.$touch()
      setTimeout(() => {
        if (!this.$v.$invalid) {
          let objetoParaEnvio = {}
          objetoParaEnvio['NOME_DEP'] = this.NOME_DEP
          objetoParaEnvio['COD_PARENTE'] = this.COD_PARENTE
          objetoParaEnvio['Idt'] = this.Idt_dep
          objetoParaEnvio['CPF'] = this.CPF_dep
          objetoParaEnvio['PROCESSO_N'] = this.prec_cp_dep
          objetoParaEnvio['COTA_PARTE'] = this.COTA_PARTE
          objetoParaEnvio['COD_POSTO'] = this.COD_POSTO_DEP.COD_POSTO
          if (typeof this.UA_PAGADORA === 'string') {
            objetoParaEnvio['UA_PAGADORA'] = this.UA_PAGADORA
          } else {
            objetoParaEnvio['UA_PAGADORA'] = this.UA_PAGADORA.UA_PAGADORA
          }
          if (this.Julgado_TCU === null) {
            objetoParaEnvio['Julgado_TCU'] = null
          } else {
            if (typeof this.Julgado_TCU === 'string') {
              objetoParaEnvio['Julgado_TCU'] = this.Julgado_TCU
            } else {
              objetoParaEnvio['Julgado_TCU'] = this.Julgado_TCU.Julgamento_TCU
            }
          }
          objetoParaEnvio['DATANASC'] = this.DATANASC
          objetoParaEnvio['INICIO_BENEF'] = this.INICIO_BENEF
          objetoParaEnvio['ALT_BENEF'] = this.ALT_BENEF
          objetoParaEnvio['FIM_BENEF'] = this.FIM_BENEF
          objetoParaEnvio['INSTITUIDOR'] = this.selectedInst

          // tem que ajustar as observações (NAO FIZ)

          if (this.editedIndex > -1) {
            objetoParaEnvio['id'] = this.id_dep
            this.$http.put('dep/altera/' + objetoParaEnvio.id, objetoParaEnvio)
              .then(response => {
                for (let i = 0; i < this.dependentes.data.length; i++) {
                  if (this.dependentes.data[i].id === response.data.id) {
                    this.dependentes.data.splice(i, 1, response.data)
                  }
                }
                this.$toastr.s(
                  'Dependente alterado com sucesso', 'Sucesso!'
                )

                this.loadingButton = false
                this.disableButton = false
              }, err => {
                console.log(err)
                this.$toastr.e(
                  'Não foi possível alterar o Dependente', 'Erro!'
                )
              })
          } else {
            objetoParaEnvio['obs'] = this.editedDep.obs
            this.$http.post('dep/salva', objetoParaEnvio)
              .then(response => {
                this.dependentes.data.push(response.data)
                this.disableButton = false
                this.loadingButton = false
                this.dialog = false
                this.$toastr.s(
                  'Dependente cadastrado com sucesso', 'Sucesso!'
                )
              }, err => {
                console.log(err)
                this.$toastr.e(
                  'Não foi possível cadastrar o Dependente', 'Erro!'
                )
              })
          }
          this.resetFieldsdDep()
          this.close()
        } else {
          this.loadingButton = false
          this.disableButton = false
          this.$toastr.e(
            'Não foi possível realizar a sua solicitação. Revise os campos do formulário.', 'Erro!'
          )
        }
      }, 1000)
    },

    unlockDataDep () {
      if (this.btnTxtDataEditDep === 'Travar Campos') {
        this.diableDataEditDep = true
        this.btnTxtDataEditDep = 'Destravar Campos'
        this.btnIcoDataEditDep = 'mdi-lock-open'
      } else {
        this.diableDataEditDep = false
        this.btnTxtDataEditDep = 'Travar Campos'
        this.btnIcoDataEditDep = 'mdi-lock'
      }
    },
    // fim

    // dependentes
    unlockDataDepBase () {
      if (this.btnTxtDataEditDepBase === 'Travar Campos') {
        this.diableDataEditDepBase = true
        this.btnTxtDataEditDepBase = 'Destravar Campos'
        this.btnIcoDataEditDepBase = 'mdi-lock-open'
      } else {
        this.diableDataEditDepBase = false
        this.btnTxtDataEditDepBase = 'Travar Campos'
        this.btnIcoDataEditDepBase = 'mdi-lock'
      }
    },

    deleteDep (item) {
      this.editedIndex = this.dependentes.data.indexOf(item)
      this.editedDep = Object.assign({}, item)
      this.dialogDeleteDep = true
    },

    deleteDepConfirm () {
      this.$http.delete('dep/' + this.editedDep.id)
      // eslint-disable-next-line no-return-assign
        .then(() => {
          for (let i = 0; i < this.dependentes.data.length; i++) {
            if (this.dependentes.data[i].id === this.editedDep.id) {
              this.dependentes.data.splice(i, 1)
            }
          }
          this.$toastr.s(
            'Dependente excluído com sucesso', 'Sucesso!'
          )
          this.dialogDeleteDep = false
          this.editedIndex = -1
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível excluir o dependente!', 'Erro!'
          )
        })
    },

    editDep (obj) {
      this.editedIndex = obj.id
      this.resetFieldsdDep()
      this.deondeVemDep = 'JanelaEdicao'
      this.diableDataEditDep = true
      this.btnTxtDataEditDep = 'Destravar Campos'
      this.btnIcoDataEditDep = 'mdi-lock-open'
      this.id_dep = obj.id
      this.NOME_DEP = obj.NOME_DEP
      this.COD_PARENTE = obj.parentesco.COD_PARENTE
      this.Idt_dep = obj.Idt
      this.CPF_dep = obj.CPF
      this.prec_cp_dep = obj.PROCESSO_N
      this.COTA_PARTE = obj.COTA_PARTE
      this.COD_POSTO_DEP = obj.posto
      this.UA_PAGADORA = obj.UA_PAGADORA
      this.Julgado_TCU = obj.Julgado_TCU
      this.DATANASC = this.formataDataHora(obj.DATANASC, 'Não')
      this.INICIO_BENEF = this.formataDataHora(obj.INICIO_BENEF, 'Não')
      this.ALT_BENEF = this.formataDataHora(obj.ALT_BENEF, 'Não')
      this.FIM_BENEF = this.formataDataHora(obj.FIM_BENEF, 'Não')
      this.editedDep = obj
      this.selectedInst = obj.instituidor
      this.dialog = true
      if (this.selectedInst) {
        this.disableButton = false
        this.loadingButton = false
      }
    },

    resetFieldsdDep () {
      this.NOME_DEP = ''
      this.COD_PARENTE = ''
      this.Idt_dep = ''
      this.CPF_dep = ''
      this.prec_cp_dep = ''
      this.COTA_PARTE = ''
      this.COD_POSTO_DEP = ''
      this.UA_PAGADORA = ''
      this.Julgado_TCU = ''
      this.INICIO_BENEF = ''
      this.dateIniBen = ''
      this.ALT_BENEF = ''
      this.dateAltBen = ''
      this.FIM_BENEF = ''
      this.dateFimBen = ''
      this.obsnewdep = []
      // list errors msg dep
      this.ErrorParentesco = ''
      this.idtDepError = ''
      this.ErrorCpf = ''
      this.ErrorNomeDep = ''
      this.postograddeperror = ''
      this.datanascerror = ''
      this.selectedInst = {}
      this.editedDep.obs = []
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        // this.editedDeCujus = Object.assign({}, this.defaultDeCujus)
        this.editedIndex = -1
        this.contadorIndexAuxObsDep = 0
        this.resetFieldsdDep()
      })
    },

    // utilitários

    formataDataHora (dataHora, ch) {
      if (dataHora === null) {
        return null
      } else if (dataHora[2] === '/') {
        return dataHora
      } else {
        let d = moment.tz(dataHora, 'America/Manaus')
        let dh
        if (ch === 'Sim') {
          dh = d.format('DD/MM/YYYY ( HH:mm:ss )')
        } else {
          dh = d.format('DD/MM/YYYY')
        }
        return dh
      }
    },

    contaTempo (inicio, fim) {
      let dini = moment.tz(inicio, 'America/Manaus')
      let dfim = moment.tz(fim, 'America/Manaus')
      var diff = moment(dfim, 'YYYY-MM-DD HH:mm:ss').diff(moment(dini, 'YYYY-MM-DD HH:mm:ss'))
      var horas = Math.round(moment.duration(diff).asDays())
      return horas
    },

    formatadorDeCPF (cpf) {
      if (cpf) {
        if (validaCpf(cpf)) {
          // retorna cpf formatado
          return ['Ok', cpf[0] + cpf[1] + cpf[2] + '.' + cpf[3] + cpf[4] + cpf[5] + '.' + cpf[6] + cpf[7] + cpf[8] + '-' + cpf[9] + cpf[10]]
        } else {
          // retorna o erro
          let ajustes
          if (cpf.length === 11) {
            ajustes = cpf[0] + cpf[1] + cpf[2] + '.' + cpf[3] + cpf[4] + cpf[5] + '.' + cpf[6] + cpf[7] + cpf[8] + '-' + cpf[9] + cpf[10]
          } else {
            ajustes = cpf
          }
          return ['Erro', 'CPF Inválido (' + ajustes + ')']
        }
      } else {
        return ['Erro', 'Erro']
      }
    },

    verificadorCpfDep (cpf) {
      if (validaCpf(cpf)) {
        let objetoEnvio = {}
        objetoEnvio['CPF'] = cpf
        objetoEnvio['ref'] = this.editedIndex
        let resposta = this.$http.post('dep/cpf', objetoEnvio)
          .then(response => {
            if (response.data === 0) {
              this.ErrorCpf = ''
            } else {
              this.ErrorCpf = 'Este CPF já se encontra cadastrado em nossa base de dados e não pode ser usado!'
            }
          })
          .catch(erro => console.log(erro))
        this.ErrorCpf = ''
        return resposta
      } else {
        this.ErrorCpf = 'Este CPF é inválido!'
      }
    },

    formatDate (date) {
      if (!date) return null
      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },

    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },

    // ---

    async searchFieldsIni () {
      await this.searchFields()
    },

    searchFields () {
      let objetoBusca = {}
      if (this.search.length < 3) {
        this.$toastr.e(
          'Digite ao menos 3 caracteres para iniciar a busca!', 'Erro!'
        )
      } else {
        objetoBusca['campo'] = this.search
        this.loadTable = true
        this.dependentes = []
        try {
          this.$http.post('dep/busca', objetoBusca)
            .then(response => {
              this.dependentes = response.data
              this.loadTable = false
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível obter informações!', 'Erro!'
              )
            })
        } catch (e) {
          console.log(e)
        }
      }
    },

    limpaBusca () {
      this.search = ''
      this.getDependentes()
    },

    showHideInfoProc () {
      if (this.exibeInfoProcShow) {
        this.exibeInfoProcShow = false
        this.textShowHideInfoProc = 'Exibir '
      } else {
        this.exibeInfoProcShow = true
        this.textShowHideInfoProc = 'Ocultar '
      }
    },

    showHideInfoInstituidor () {
      if (this.exibeInfoInstituidor) {
        this.exibeInfoInstituidor = false
        this.textShowHideInfoInstituidor = 'Exibir '
      } else {
        this.exibeInfoInstituidor = true
        this.textShowHideInfoInstituidor = 'Ocultar '
      }
    },

    doBuscaInstituidor () {
      let objetoBusca = {}
      if (this.busca_instituidor.length < 3) {
        this.$toastr.e(
          'Digite ao menos 3 caracteres para iniciar a busca!', 'Erro!'
        )
      } else {
        objetoBusca['campo'] = this.busca_instituidor
        try {
          this.$http.post('inativo/busca', objetoBusca)
            .then(response => {
              this.inativos = response.data
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível obter informações!', 'Erro!'
              )
            })
        } catch (e) {
          console.log(e)
        }
      }
    },

    selectInstituidor (item) {
      this.selectedInst = Object.assign({}, item)
      this.busca_instituidor = ''
      this.inativos = {}
      this.disableButton = false
    },

    resetaInstituidor () {
      if (this.editedIndex === -1) {
        this.selectedInst = {}
      } else {
        this.temptempInstituidor = this.selectedInst
        this.selectedInst = {}
      }
    },

    travarBotaoSalvar () {
      this.disableButton = true
    },

    cancelBuscaInstituidor () {
      if (this.editedIndex === -1) {
        this.dialog = false
        this.editedIndex = -1
      } else {
        this.selectedInst = this.temptempInstituidor
      }
    }

  }
}

</script>

<style>
  .v-messages__message {
    color: red;
  }
</style>



