<template>

  <v-main>

    <v-container class="pt-5">

      <!--Banner-->
      <v-banner
        class="p-5"
        elevation="21">

        <v-icon
          size="36"
          slot="icon">
          fab fa-fort-awesome-alt
        </v-icon>

        <h2>Gerenciamento de Seções </h2>

      </v-banner>

      <!--DataTable-->
      <v-data-table
        :headers="headers"
        :items="secoes"
        :search="search"
        class="elevation-21 mt-4"
      >
        <template v-slot:top>

          <v-toolbar
            flat>

            <v-toolbar-title>Tabela de Seções Cadastradas</v-toolbar-title>

            <v-divider
              class="mx-4"
              inset
              vertical></v-divider>

            <!--Dialog de cadastro e edição-->
            <v-dialog
              max-width="50%"
              persistent v-model="dialog">

              <!--novo cadastro-->
              <template v-slot:activator="{ on, attrs }">

                <v-btn
                  class="mb-2"
                  color="primary"
                  dark
                  v-bind="attrs"
                  v-on="on">

                  <v-icon class="mr-5" small>
                    mdi-message-plus-outline
                  </v-icon>

                  Nova Seção

                </v-btn>

              </template>

              <!--Card de cadastro edição-->
              <v-form>

                <v-card>

                  <v-card-title>

                    <v-icon class="mr-4">
                      fab fa-fort-awesome-alt
                    </v-icon>
                    <span class="headline">
                       {{ formTitle }}</span>

                  </v-card-title>

                  <v-card-text>

                    <v-container>

                      <v-row>

                        <!--nome - sigla - permissoes -->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4">

                          <!--Om vinculante-->
                          <v-row>
                            <v-col>
                              <v-select
                                :error-messages="OmErrors"
                                :items="oms"
                                @blur="$v.om.$touch()"
                                @change="buscaSecPerm(om.id)"
                                @input="$v.om.$touch()"
                                dense
                                hint="Selecione a Om a qual esta estará vinculada"
                                item-text="sigla"
                                item-value="id"
                                label="Om Vinculante"
                                name="om"
                                outlined
                                required
                                return-object
                                v-model="om"
                              ></v-select>
                            </v-col>
                          </v-row>

                          <!--Nome-->
                          <v-row>

                            <v-col>

                              <v-text-field
                                :error-messages="nomeErrors"
                                @blur="$v.nome.$touch()"
                                @input="$v.nome.$touch()"
                                dense
                                hint="Por exemplo: Seção de Atendimento"
                                label="Nome"
                                name="nome"
                                outlined
                                placeholder="Insira o Nome da Seção"
                                required
                                v-model="nome">

                              </v-text-field>

                            </v-col>

                          </v-row>

                          <!--sigla-->
                          <v-row>

                            <v-col>

                              <v-text-field
                                :error-messages="siglaErrors"
                                @blur="$v.sigla.$touch()"
                                @input="$v.sigla.$touch()"
                                dense
                                hint="Por exemplo: SS.3"
                                label="Sigla da Seção"
                                name="sigla"
                                outlined
                                placeholder="Insira a sigla da Seção"
                                required
                                v-model="sigla">
                              </v-text-field>

                            </v-col>

                          </v-row>

                          <!--secao pai-->
                          <v-row v-if="secoesAjustadas.length > 0">
                            <v-col>
                              <v-select
                                :error-messages="SecPaiErrors"
                                :items="secoesAjustadas"
                                @blur="$v.secpai.$touch()"
                                @input="$v.secpai.$touch()"
                                dense
                                hint="Selecione a seção a qual esta estará subordinada"
                                item-text="sigla"
                                item-value="id"
                                label="Seção Pai"
                                name="secao"
                                outlined
                                required
                                return-object
                                v-model="secpai"
                              ></v-select>
                            </v-col>
                          </v-row>

                          <!--Permissões-->
                          <v-row>

                            <v-col>
                              Permissões: <br>

                              <v-row>

                                <!--Administrador, Homologador e Delegado-->
                                <v-col>
                                  <!--Administrador-->
                                  <v-checkbox
                                    hide-details
                                    label="Administrador Geral"
                                    v-model="permissoes"
                                    value="Administrador Geral"
                                  ></v-checkbox>
                                  <!--Homologador-->
                                  <v-checkbox
                                    hide-details
                                    label="Homologador"
                                    v-model="permissoes"
                                    value="Homologador"
                                  ></v-checkbox>
                                  <!--Delegado-->
                                  <v-checkbox
                                    hide-details
                                    label="Delegado"
                                    v-model="permissoes"
                                    value="Delegado"
                                  ></v-checkbox>
                                </v-col>

                                <!--Executor e Visualizador-->
                                <v-col>
                                  <!--Administrador-->
                                  <v-checkbox
                                    hide-details
                                    label="Administrador"
                                    v-model="permissoes"
                                    value="Administrador"
                                  ></v-checkbox>
                                  <!--Executor-->
                                  <v-checkbox
                                    hide-details
                                    label="Executor"
                                    v-model="permissoes"
                                    value="Executor"
                                  ></v-checkbox>
                                  <!--Visualizador-->
                                  <v-checkbox
                                    hide-details
                                    label="Visualizador"
                                    v-model="permissoes"
                                    value="Visualizador"
                                  ></v-checkbox>
                                </v-col>

                              </v-row>

                              <!--Alert de erro se não tiver nenuma selecionada-->
                              <v-row v-if="permissoes.length === 0">
                                <v-col>
                                  <v-alert type="error">Você deve selecionar ao menos uma permissão</v-alert>
                                </v-col>
                              </v-row>

                            </v-col>

                          </v-row>

                        </v-col>
                        <!--COR-->
                        <v-col
                          cols="12"
                          md="6"
                          sm="4">

                          <v-card
                            elevation="2">

                            <v-container fluid>

                              <v-row>

                                <v-col>
                                  <p><b>Cor de referencia da Seção</b></p>
                                  <v-color-picker
                                    canvas-height="150"
                                    hide-inputs
                                    mode="hexa"
                                    name="cor"
                                    v-model="cor"
                                    width="500">
                                  </v-color-picker>

                                  <v-alert type="info">
                                    Apenas para gerenciamento de estatísticas.
                                  </v-alert>

                                </v-col>

                              </v-row>

                            </v-container>

                          </v-card>

                        </v-col>

                      </v-row>

                      <!--Botões-->
                      <v-row>

                        <v-col>

                          <!--Salvar-->
                          <v-btn
                            @click="save"
                            color="green darken-1">
                            Salvar
                          </v-btn>

                          <!--Cancelar-->
                          <v-btn
                            @click="close"
                            class="ml-6"
                            color="red darken-1">
                            Cancelar
                          </v-btn>

                        </v-col>

                      </v-row>

                    </v-container>

                  </v-card-text>

                </v-card>

              </v-form>

            </v-dialog>

            <v-spacer></v-spacer>

            <!--Pesquisar-->
            <v-text-field
              append-icon="mdi-magnify"
              hide-details
              label="Pesquisar"
              placeholder="Pesquisar"
              single-line
              v-model="search">
            </v-text-field>

            <!--Dialog para deletar seção-->
            <v-dialog max-width="800px" v-model="dialogDelete">

              <v-card>

                <v-card-title class="justify-center" primary-title>

                  <v-icon class="mr-4">fa fa-exclamation-triangle</v-icon>
                  Você tem certeza que quer deletar a Seção?
                  <v-icon class="ml-4">fa fa-exclamation-triangle</v-icon>

                </v-card-title>

                <v-card-text>
                  <div class="text-center">Essa ação é irreversível. Tenha certeza do que está fazendo.</div>
                </v-card-text>

                <v-card-actions class="pb-5">

                  <v-spacer></v-spacer>

                  <v-btn
                    @click="closeDelete"
                    color="grey lighten-1">
                    Cancelar
                  </v-btn>

                  <span class="pl-5 pr-5"></span>

                  <v-btn
                    @click="deleteSecaoConfirm"
                    color="red lighten-1">
                    Excluir
                  </v-btn>

                </v-card-actions>

              </v-card>

            </v-dialog>

            <!--Dialog para ver permissões de seção-->
            <v-dialog max-width="800px" v-model="dialogPermissions">

              <v-card>

                <v-card-title primary-title>Permissões da {{ secaoShowPermissao}}</v-card-title>

                <v-card-text>
                  <ul>
                    <li :key="lePermissao.id" v-for="lePermissao in secaoListPermissao"> {{ lePermissao.permissao }}
                    </li>
                  </ul>
                </v-card-text>

                <v-card-actions class="pb-5">
                  <v-spacer></v-spacer>
                  <v-btn @click="dialogPermissions = false" color="grey lighten-1">Fechar</v-btn>
                </v-card-actions>

              </v-card>

            </v-dialog>

          </v-toolbar>

        </template>

        <!--Template de botões para editar e excluir -->
        <template v-slot:item.actions="{ item }">

          <!--ver Permissoes-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="permissaoSecao(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on">
                mdi-magnify-plus
              </v-icon>
            </template>
            <span>Ver permissões da seção</span>
          </v-tooltip>

          <!--editar-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="editSecao(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on">
                mdi-pencil
              </v-icon>
            </template>
            <span>Editar Seção</span>
          </v-tooltip>

          <!--Excluir-->
          <v-tooltip top>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                @click="deleteSecao(item)"
                class="mr-2"
                small
                v-bind="attrs"
                v-on="on">
                mdi-delete
              </v-icon>
            </template>
            <span>Excluir Seção</span>
          </v-tooltip>
        </template>

        <!--Ajuste de cores-->
        <template v-slot:item.cor="{ item }">
          <v-chip
            :color="getColor(item.cor)"
            dark>
          </v-chip>
        </template>

      </v-data-table>

      <!--Organograma-->
      <v-card class="mt-5" elevation="21">
        <v-card-title>Organograma</v-card-title>
        <v-card-text>

          <!--Botões de organograma-->

          <v-row>
            <v-col :key="lebtn.id" cols="3" v-for="lebtn in dataBtnPai">
              <v-btn :color="lebtn.cor" @click="showOrgSec(lebtn.id)">{{lebtn.sigla}}</v-btn>
            </v-col>
          </v-row>

          <v-row v-if="dataChart.title !== 'Selecione uma opção'">
            <v-col class="btn-wrap">
              <v-btn @click="closeChart" class="error">
                <v-icon class="mr-3">
                  mdi-close-circle-outline
                </v-icon>
                Fechar
              </v-btn>
              <organization-chart :datasource="dataChart" pan></organization-chart>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>

    </v-container>

  </v-main>

</template>
<script>import {validationMixin} from 'vuelidate'
import {required} from 'vuelidate/lib/validators'
import OrganizationChart from 'vue-organization-chart'
import 'vue-organization-chart/dist/orgchart.css'

export default {
  components: {
    OrganizationChart
  },
  mixins: [validationMixin],
  directives: {},
  validations: {
    nome: {required},
    sigla: {required},
    om: {required},
    secpai: {required}
  },

  data: () => ({
    // Itens de tabelas
    headers: [
      {
        text: 'Nome',
        align: 'start',
        value: 'nome'
      },
      {
        text: 'Sigla',
        value: 'sigla'
      },
      {
        text: 'Seção Pai',
        value: 'pai.sigla'
      },
      {
        text: 'Om',
        value: 'om.sigla'
      },
      {
        text: 'Cor',
        value: 'cor',
        align: 'center'
      },
      {
        text: 'Actions',
        value: 'actions',
        align: 'center',
        sortable: false
      }
    ],
    search: '',
    // Para edição e cadastro
    dialog: false,
    editedIndex: -1,
    nome: '',
    sigla: '',
    om: '',
    cor: '',
    secoesPermitidas: [],
    permissoes: [],
    // Mostrar Permissões
    dialogPermissions: false,
    secaoShowPermissao: '',
    secaoListPermissao: [],
    dataChart: {
      id: 1,
      title: 'Selecione uma opção',
      name: 'Selecione uma opção',
      children: []
    },
    // para deletar
    dialogDelete: false,
    defaultSecao: {
      nome: '',
      sigla: '',
      cor: '',
      pai: '',
      om: '',
      secao_pai: '',
      permissoes: []
    },
    editedSecao: {
      nome: '',
      sigla: '',
      om: '',
      cor: '',
      pai: '',
      secao_pai: '',
      permissoes: []
    },
    // subordinacao
    secpai: '',
    pai: '',
    secoesAjustadas: []
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nova Seção' : 'Editar Seção'
    },
    nomeErrors () {
      const errors = []
      if (!this.$v.nome.$dirty) return errors
      !this.$v.nome.required && errors.push('O Campo "Nome" não pode ficar em branco! ')
      return errors
    },
    siglaErrors () {
      const errors = []
      if (!this.$v.sigla.$dirty) return errors
      !this.$v.sigla.required && errors.push('O Campo "Sigla" não pode ficar em branco! ')
      return errors
    },
    SecPaiErrors () {
      const errors = []
      if (!this.$v.secpai.$dirty) return errors
      !this.$v.secpai.required && errors.push('O Campo "Seção Pai" não pode ficar em branco! ')
      return errors
    },
    OmErrors () {
      const errors = []
      if (!this.$v.om.$dirty) return errors
      !this.$v.om.required && errors.push('O Campo "Om Vinculante" não pode ficar em branco! ')
      return errors
    },
    showColor () {
      if (typeof this.cor === 'string') return this.cor

      return JSON.stringify(Object.keys(this.cor).reduce((cor, key) => {
        cor[key] = Number(this.cor[key].toFixed(2))
        return cor
      }, {}), null, 2)
    }
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
    cor (value) {
      // temporary fix while there is no way to disable the alpha channel in the colorpicker component: https://github.com/vuetifyjs/vuetify/issues/9590
      if (value.toString().match(/#[a-zA-Z0-9]{8}/)) {
        this.cor = value.substr(0, 7)
      }
    }
  },

  props: {
    secoes: Array,
    oms: Array,
    dataBtnPai: Array
  },

  methods: {

    buscaSecPerm (id) {
      this.$http.get('/secoes/secoespermitidas/' + id)
        .then(response => {
          this.secoesAjustadas = []
          if (this.editedIndex === -1) {
            this.secoesAjustadas.push({
              cor: '#000000',
              id: 0,
              nome: 'Seção Pai',
              pai: 0,
              permissoes: [],
              secao_pai: 0,
              sigla: 'Seção Pai'
            })
            for (let i = 0; i < response.data.length; i++) {
              this.secoesAjustadas.push(response.data[i])
            }
          } else {
            for (let i = 0; i < response.data.length; i++) {
              if (response.data[i].id === this.editedSecao.id) {
                this.secoesAjustadas.push({
                  cor: response.data[i].cor,
                  id: response.data[i].id,
                  nome: response.data[i].nome,
                  sigla: 'Seção Pai'
                })
              } else {
                this.secoesAjustadas.push(response.data[i])
              }
            }
          }
        })
        .catch(erro => console.log(erro))
    },

    editSecao (item) {
      this.editedIndex = this.secoes.indexOf(item)
      this.editedSecao = Object.assign({}, item)
      this.id = this.editedSecao.id
      this.nome = this.editedSecao.nome
      this.sigla = this.editedSecao.sigla
      this.secpai = this.editedSecao.pai
      this.om = this.editedSecao.om
      this.cor = this.editedSecao.cor
      this.permissoes = []
      this.secoesAjustadas = []
      if (this.editedIndex !== -1) {
        let arraySecAjust = []
        for (let i = 0; i < this.secoes.length; i++) {
          if (this.secoes[i].id === this.id) {
            arraySecAjust.push({
              cor: this.secoes[i].cor,
              id: this.secoes[i].id,
              nome: this.secoes[i].nome,
              sigla: 'Seção Pai'
            })
          } else {
            arraySecAjust.push(this.secoes[i])
          }
        }
        this.secoesAjustadas = arraySecAjust
      }

      for (let i = 0; i < this.editedSecao.permissoes.length; i++) {
        this.permissoes.push(this.editedSecao.permissoes[i].permissao)
      }
      this.$v.$touch()
      this.dialog = true
    },

    getColor (cor) {
      return cor
    },

    deleteSecao (item) {
      this.editedIndex = this.secoes.indexOf(item)
      this.editedSecao = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteSecaoConfirm () {
      this.$http.delete('secoes/' + this.editedSecao.id)
      // eslint-disable-next-line no-return-assign
        .then(() => {
          this.secoes.splice(this.editedIndex, 1)
          this.resetFields()
          this.$toastr.s(
            'Seção removida com sucesso', 'Sucesso!'
          )
          this.closeDelete()
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover a Seção', 'Erro!'
          )
        })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedSecao = Object.assign({}, this.defaultSecao)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    resetFields () {
      this.id = ''
      this.nome = ''
      this.sigla = ''
      this.secpai = ''
      this.om = ''
      this.pai = ''
      this.cor = ''
      this.permissoes = []
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedSecao = Object.assign({}, this.defaultSecao)
        this.editedIndex = -1
        this.resetFields()
      })
    },

    permissaoSecao (item) {
      this.secaoShowPermissao = item.sigla
      this.secaoListPermissao = item.permissoes
      this.dialogPermissions = true
    },

    save () {
      this.$v.$touch()
      if (!this.$v.$invalid && this.permissoes.length !== 0) {
        let objetoParaEnvio = {}
        objetoParaEnvio['id'] = this.id
        objetoParaEnvio['nome'] = this.nome
        objetoParaEnvio['sigla'] = this.sigla
        objetoParaEnvio['secpai'] = this.secpai
        objetoParaEnvio['cor'] = this.cor
        objetoParaEnvio['om_id'] = this.om.id
        objetoParaEnvio['permissoes'] = this.permissoes
        if (this.editedIndex > -1) {
          // manda para edição
          this.$http.put('secoes/' + this.id, objetoParaEnvio)
            .then(response => {
              Object.assign(this.secoes[this.editedIndex], response.data)
              this.$toastr.s(
                'Seção alterada com sucesso', 'Sucesso!'
              )
              this.close()
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível alterar a Seção', 'Erro!'
              )
            })
        } else {
          // manda para criação
          this.$http.post('secoes', objetoParaEnvio)
            .then(response => {
              this.secoes.push(response.data)
              this.$toastr.s(
                'Seção criada com sucesso', 'Sucesso!'
              )
              this.resetFields()
              this.close()
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível criar a Seção', 'Erro!'
              )
            })
        }
        this.$http.get('secoes/lepais')
          .then(response => {
            this.$emit('resetarPais', response.data)
          }, err => {
            console.log(err)
            this.$toastr.e(
              'Não foi possível obter informações', 'Erro!'
            )
          })
      }
    },

    showOrgSec (id) {
      this.$http.get('secoes/chart/' + id)
        .then(response => {
          this.dataChart = response.data
        })
        .catch(erro => console.log(erro))
    },

    closeChart () {
      this.dataChart = {
        id: 1,
        title: 'Selecione uma opção',
        name: 'Selecione uma opção',
        children: []
      }
    }
  }
}

</script>

<style>
  .btn-wrap {
    position: relative;
    text-align: center;
  }

  .btn-wrap button {
    position: absolute;
    z-index: 1;
    top: 10%;
    left: 6%;
    padding: 0;
    margin: -15px 0 0 -30px; /* margem à esquerda e ao topo metade da medida do botão */
    border: 0 none;
  }
</style>

<!--secoes do select devem vir de acordo com a OM selecionada-->
