<template>
  <v-main>
    <v-container fluid>
      <v-row class="mt-5 ml-5 mr-5">
        <v-col>
          <!--Dados do usuário-->
          <v-card>
            <v-card-title>Dados do usuário</v-card-title>
            <v-card-text>
              <v-row>
                <v-col>
                  <b>Nome: </b>{{ usuarioLogado.nome}}<br>
                  <b>Nome de Guerra: </b>{{ usuarioLogado.guerra}}<br>
                  <b>Cpf: </b> {{ usuarioLogado.cpf }}<br>
                  <b>Tel: </b> {{ usuarioLogado.tel }}<br>
                  <b>Seção: </b> {{ usuarioLogado.secao.sigla }}<br>
                  <b>Om: </b> {{ usuarioLogado.secao.om.sigla }}<br><br>
                  <b>Permissões:</b>
                  <ul>
                    <li :key="`lemiperm_${perm}`" v-for="perm in usuarioLogado.permissoes"> {{ perm }}</li>
                  </ul>
                  <b></b>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col
          cols="6"
        >
          <v-card
            elevation="10"
            outlined
            rounded="lg"
          >
            <v-row>
              <v-col cols="10" offset="1">
                <div class="text-center pt-5">
                  <h1>Bem-vindo ao Morto!</h1>
                  <p>Sistema web para Controle de Dados de Decujos</p>
                </div>
              </v-col>
            </v-row>
          </v-card>
          <br>

          <v-card
            elevation="10"
            outlined
            rounded="lg"
          >
            <v-row>
              <v-col cols="10" offset="1">
                <div class="text-center">
                  <h1>Estatísticas para Administradores</h1>
                  <br>
                  <p>
                    <v-icon>mdi-worker</v-icon>
                    Em construção
                  </p>
                </div>
              </v-col>
            </v-row>
          </v-card>

        </v-col>
        <v-col>

        </v-col>
      </v-row>
    </v-container>
  </v-main>
</template>

<script>import {logoutMixin} from '@/mixins'
import {mapGetters} from 'vuex'

export default {
  name: 'home',
  mixins: [logoutMixin],
  methods: {},
  computed: {

    ...mapGetters(['usuarioLogado'])

  }
}
</script>



