    },

    searchFields () {
      let objetoBusca = {}
      if (this.search.length < 3) {
        this.$toastr.e(
          'Digite ao menos 3 caracteres para iniciar a busca!', 'Erro!'
        )
      } else {
        objetoBusca['campo'] = this.search
        this.loadTable = true
        this.dependentes = []
        try {
          this.$http.post('inativo/busca', objetoBusca)
            .then(response => {
              this.dependentes = response.data
              this.loadTable = false
            }, err => {
              console.log(err)
              this.$toastr.e(
