    // TIPO DE PROCESSO
    salvatipoproc () {
      if (this.prazoprocesso === '') {
        this.errorPrazoProc = 'Você deve estipular um prazo máximo de duração para o processo'
      } else {
        this.errorPrazoProc = ''
      }

      if (this.tipoprocesso === '') {
        this.errorTipoProc = 'O nome do tipo de processo não pode ficar em branco'
      } else {
        this.errorTipoProc = ''
      }

      // manda para salvar o tipo
      if (this.prazoprocesso !== '' && this.tipoprocesso !== '') {
        let objetoParaEnvio = {}
        objetoParaEnvio['documentos'] = this.objetoDocExigido
        objetoParaEnvio['tipoproc'] = this.tipoprocesso
        objetoParaEnvio['prazoprocesso'] = this.prazoprocesso

        this.$http.post('proctipo', objetoParaEnvio)
          .then(response => {
            this.tiposprocessos.push(response.data)

            this.showHideCadastraTipo = false
            this.mostraescondebotaocadastratipo = true
            this.objetoDocExigido = Object.assign([{}], this.defaultObjetoDocExigido)

            this.$toastr.s(
              'Tipo de processo cadastrado com sucesso', 'Sucesso!'
            )
          }, err => {
            console.log(err)
            this.$toastr.e(
              'Não foi possível cadastrar o Tipo de Processo', 'Erro!'
            )
          })
      }
    },

    showDetTipoProc (item) {
      this.objetoTipoProcesso = Object.assign(this.objetoTipoProcesso, item)
      this.dialogShowTipoProc = true
    },

    editTipoProc (item) {
      this.objetoTipoProcesso = Object.assign(this.objetoTipoProcesso, item)
      this.dialogEditTipoProc = true
      this.tipoprocesso = this.objetoTipoProcesso.tipo
      this.prazoprocesso = this.objetoTipoProcesso.prazo
    },

    doEditTipoProc () {
      if (this.tipoprocesso === '') {
        this.errorTipoProc = 'O campo tipo de processo não pode ficar em branco'
      } else {
        this.errorTipoProc = ''
      }

      if (this.prazoprocesso === '') {
        this.errorPrazoProc = 'O campo prazo para execução não pode ficar em branco'
      } else {
        this.errorPrazoProc = ''
      }

      if (this.prazoprocesso !== '' && this.tipoprocesso !== '') {
        let objetoParaEnvio = {}
        objetoParaEnvio['id'] = this.objetoTipoProcesso.id
        objetoParaEnvio['tipo'] = this.tipoprocesso
        objetoParaEnvio['prazo'] = this.prazoprocesso
        this.$http.put('proctipo/' + objetoParaEnvio.id, objetoParaEnvio)
          .then(response => {
            for (let i = 0; i < this.tiposprocessos.length; i++) {
              if (this.tiposprocessos[i].id === response.data.id) {
                this.tiposprocessos.splice(i, 1, response.data)
              }
            }
            this.dialogEditTipoProc = false
            this.$toastr.s(
              'Tipo de Documento alterado com sucesso', 'Sucesso!'
            )
          }, err => {
            console.log(err)
            this.$toastr.e(
              'Não foi possível alterar o Tipo de Documento', 'Erro!'
            )
          })
      }
    },

    deleteTipoProc (item) {
      this.dialogDeleteTipoProc = true

      this.$http.get('proctipo/consulta/' + item.id)
        .then(response => {
          if (response.data === 0) {
            this.titleDialogDelete = 'Você tem certeza que quer deletar este tipo de processo?'
            this.textDialogDelete = 'Essa ação é irreversível. Tenha certeza do que está fazendo.'
            this.habDesabBtnExcTipoProc = false
            this.idTipoProcDelete = item.id
          } else {
            this.titleDialogDelete = 'Você não pode realizar essa ação?'
            this.textDialogDelete = 'Isso ocorre pois existem processos vinculados a este tipo cadastrado. Para conseguir excluir esse tipo, você necessariamente precisa ou excluir todos os processos vinculados a ele, ou alterar esses processos para um tipo já existente.'
            this.habDesabBtnExcTipoProc = true
            this.idTipoProcDelete = ''
          }
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível retornar informações sobre o Tipo de Processo', 'Erro!'
          )
        })
    },

    doDeleteTipoProc () {
      this.$http.delete('proctipo/' + this.idTipoProcDelete)
      // eslint-disable-next-line no-return-assign
        .then(response => {
          for (let i = 0; i < this.tiposprocessos.length; i++) {
            if (this.tiposprocessos[i].id === this.idTipoProcDelete) {
              this.tiposprocessos.splice(i, 1)
            }
          }
          this.dialogDeleteTipoProc = false
          this.$toastr.s(
            'Tipo de processo removido com sucesso', 'Sucesso!'
          )
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover o Tipo de Processo', 'Erro!'
          )
        })
    },

