      this.dialogEditAddDocExigido = true
    },

    executaEditAddDocExigido () {
      if (this.leDocExigTemp === '') {
        this.errorDocExigTemp = 'O nome do documento exigido não pode ficar em branco'
      } else {
        this.errorDocExigTemp = ''
        let objetoParaEnvio = {}

        if (this.acaoFazEditAddDoc === 'edita') {
          objetoParaEnvio['id'] = this.objDocEditLeTemp.id
          objetoParaEnvio['documento'] = this.leDocExigTemp
          this.$http.put('docexig/' + objetoParaEnvio.id, objetoParaEnvio)
            .then(response => {
              for (let i = 0; i < this.objetoTipoProcesso.exigencias.length; i++) {
                if (this.objetoTipoProcesso.exigencias[i].id === response.data.id) {
                  this.objetoTipoProcesso.exigencias.splice(i, 1, response.data)
                }
              }
              this.$toastr.s(
                'Documento alterado com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível alterar o Documento', 'Erro!'
              )
            })
        } else {
          objetoParaEnvio['processo_tipo_id'] = this.objetoTipoProcesso.id
          objetoParaEnvio['documento'] = this.leDocExigTemp
          this.$http.post('docexig', objetoParaEnvio)
            .then(response => {
              this.objetoTipoProcesso.exigencias.push(response.data)
              this.$toastr.s(
                'Documento cadastrado com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível cadastrar o Documento', 'Erro!'
              )
            })
        }

        this.dialogEditAddDocExigido = false
      }
    },

    deleteDocExigidoTemp (item) {
      this.objDocEditLeTemp = item
      this.dialogDeleteDocExigidoTemp = true
    },

    doDeleteDocExigTemp () {
      this.$http.delete('docexig/' + this.objDocEditLeTemp.id)
      // eslint-disable-next-line no-return-assign
        .then(response => {
          for (let i = 0; i < this.objetoTipoProcesso.exigencias.length; i++) {
            if (this.objetoTipoProcesso.exigencias[i].id === this.objDocEditLeTemp.id) {
              this.objetoTipoProcesso.exigencias.splice(i, 1)
            }
          }
          this.dialogDeleteDocExigidoTemp = false
          this.$toastr.s(
            'Documento exigido removido com sucesso', 'Sucesso!'
          )
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover o Documento exigido', 'Erro!'
          )
        })
    },

    // STATUS

    openAjustaStatus () {
      this.objetoStatus = Object.assign([{}], this.defaultObjetoStatus)
      this.dialogAjustaStatus = true
    },

    addEditStatusTipoProc (tipo, item) {
      this.errorStatusTemp = ''
      this.acaoFazEditAddStatus = tipo
      if (tipo === 'edita') {
        this.titleCardEditAltStatus = 'Edição de'
        this.textBtnAddEditStatus = 'Alterar'
        this.leStatusTemp = item.status
        this.leStatusIdTemp = item.id
      } else {
        this.titleCardEditAltStatus = 'Cadastramento de'
        this.textBtnAddEditStatus = 'Salvar'
        this.leStatusTemp = ''
        this.leStatusIdTemp = ''
      }
      this.dialogEditAddStatus = true
    },

    deleteStatusTemp (item) {
      this.statusTempId = item.id
      this.dialogDeleteStatusTemp = true
    },

    doDeleteStatusTemp () {
      this.$http.delete('procstatus/' + this.statusTempId)
      // eslint-disable-next-line no-return-assign
        .then(response => {
          for (let i = 0; i < this.tiposstatus.length; i++) {
            if (this.tiposstatus[i].id === this.statusTempId) {
              this.tiposstatus.splice(i, 1)
            }
          }
          this.dialogDeleteStatusTemp = false
          this.$toastr.s(
            'Status removido com sucesso', 'Sucesso!'
          )
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover o Status', 'Erro!'
          )
        })
    },

    executaEditAddStatus () {
      if (this.leStatusTemp === '') {
        this.errorStatusTemp = 'O campo status não pode ficar em branco'
      } else {
        this.errorStatusTemp = ''
        let objetoParaEnvio = {}

        if (this.acaoFazEditAddStatus === 'edita') {
          objetoParaEnvio['id'] = this.leStatusIdTemp
          objetoParaEnvio['status'] = this.leStatusTemp
          this.$http.put('procstatus/' + objetoParaEnvio.id, objetoParaEnvio)
            .then(response => {
              for (let i = 0; i < this.tiposstatus.length; i++) {
                if (this.tiposstatus[i].id === response.data.id) {
                  this.tiposstatus.splice(i, 1, response.data)
                }
              }
              this.$toastr.s(
                'Status alterado com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível alterar o Status', 'Erro!'
              )
            })
        } else {
          objetoParaEnvio['status'] = this.leStatusTemp
          this.$http.post('procstatus', objetoParaEnvio)
            .then(response => {
              this.tiposstatus.push(response.data)
              this.$toastr.s(
                'Status cadastrado com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível cadastrar o Status', 'Erro!'
              )
            })
        }

        this.dialogEditAddStatus = false
      }
    },

    // SITUAÇÃO MILITAR

    openAjustaSitMil () {
      this.objetoSitMil = Object.assign([{}], this.defaultObjetoSitMil)
      this.dialogAjustaSitMil = true
    },

    addEditSitMilProc (tipo, item) {
      this.errorSitMilTemp = ''
      this.acaoFazEditAddSitMil = tipo
      if (tipo === 'edita') {
        this.titleCardEditAltSitMil = 'Edição de'
        this.textBtnAddEditSitMil = 'Alterar'
        this.leSitMilTemp = item.SITUAÇÃO_MILITAR
        this.leSitMilIdTemp = item.id
      } else {
        this.titleCardEditAltSitMil = 'Cadastramento de'
        this.textBtnAddEditSitMil = 'Salvar'
        this.leSitMilTemp = ''
        this.leSitMilIdTemp = ''
      }
      this.dialogEditAddSitMil = true
    },

    executaEditAddSitMil () {
      if (this.leSitMilTemp === '') {
        this.errorSitMilTemp = 'O campo Situação não pode ficar em branco'
      } else {
        this.errorSitMilTemp = ''
        let objetoParaEnvio = {}
        if (this.acaoFazEditAddSitMil === 'edita') {
          objetoParaEnvio['id'] = this.leSitMilIdTemp
          objetoParaEnvio['SITUAÇÃO_MILITAR'] = this.leSitMilTemp
          this.$http.put('sitmil/' + objetoParaEnvio.id, objetoParaEnvio)
            .then(response => {
              for (let i = 0; i < this.sitmilopt.length; i++) {
                if (this.sitmilopt[i].id === response.data.id) {
                  this.sitmilopt.splice(i, 1, response.data)
                }
              }
              this.$toastr.s(
                'Situação Militar alterada com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível alterar a Situação Militar', 'Erro!'
              )
            })
        } else {
          objetoParaEnvio['SITUAÇÃO_MILITAR'] = this.leSitMilTemp
          this.$http.post('sitmil', objetoParaEnvio)
            .then(response => {
              this.sitmilopt.push(response.data)
              this.$toastr.s(
                'Situação Militar cadastrada com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível cadastrar a Situação Militar', 'Erro!'
              )
            })
        }

        this.dialogEditAddSitMil = false
      }
    },

    deleteSitMilTemp (item) {
      this.sitMilTempId = item.id
      this.dialogDeleteSitMilTemp = true
    },

    doDeleteSitMilTemp () {
      this.$http.delete('sitmil/' + this.sitMilTempId)
      // eslint-disable-next-line no-return-assign
        .then(response => {
          for (let i = 0; i < this.sitmilopt.length; i++) {
            if (this.sitmilopt[i].id === this.sitMilTempId) {
              this.sitmilopt.splice(i, 1)
            }
          }
          this.dialogDeleteSitMilTemp = false
          this.$toastr.s(
            'Situação Militar removida com sucesso', 'Sucesso!'
          )
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível remover a Situação Militar', 'Erro!'
          )
        })
    },

    // PROCESSOS

    showSpaceProc (qual) {
      if (qual === 'legado') {
        this.procLegadoSpace = true
        this.showSelectedLegadoProc = true
        this.procFisicoSpace = false
        this.showSelectedFisicoProc = false
        this.procDigitalSpace = false
        this.showSelectedDigitalProc = false
      } else if (qual === 'fisico') {
        this.procLegadoSpace = false
        this.showSelectedLegadoProc = false
        this.procFisicoSpace = true
        this.showSelectedFisicoProc = true
        this.procDigitalSpace = false
        this.showSelectedDigitalProc = false
      } else {
        this.procLegadoSpace = false
        this.showSelectedLegadoProc = false
        this.procFisicoSpace = false
        this.showSelectedFisicoProc = false
        this.procDigitalSpace = true
        this.showSelectedDigitalProc = true
      }
    },

    addNewProcesso (qual, acao) {
      if (acao === 'add') {
        this.titleCardEditAltProcesso = 'Cadastramento de'
        this.textBtnAddEditProcesso = 'Salvar'
        this.requerentes = this.selectedInst.deps
      } else {
        console.log('editar')
      }
      this.dialogEditAddProcesso = true
    },

    // obs de cujus
    openNewObs (cp) {
      this.deondeVemObs = 'JanelaDetalhe'
      this.lecpdecujus = cp
      this.leObs = ''
      this.dialogNewObs = true
    },

    excluiObs (item, deondevemlaobs) {
      this.objetoObs = item
      this.dialogDeleteObs = true
      this.deondeVemObs = deondevemlaobs
    },

    deleteObsConfirm () {
      this.$http.delete('observacao/' + this.objetoObs.id)
      // eslint-disable-next-line no-return-assign
        .then(() => {
          if (this.deondeVemObs === 'JanelaEdicao' || this.deondeVemObs === 'JanelaDetalhe') {
            for (let i = 0; i < this.selectedInst.obs.length; i++) {
              if (this.selectedInst.obs[i].id === this.objetoObs.id) {
                this.selectedInst.obs.splice(i, 1)
              }
            }
          }
          this.$toastr.s(
            'Observação excluída com sucesso', 'Sucesso!'
          )
          this.dialogDeleteObs = false
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível excluir a observação!', 'Erro!'
          )
        })
    },

    submeteNewObs () {
      if (this.leObs.length !== 0) {
        if (this.deondeVemObs === 'JanelaDetalhe' || this.deondeVemObs === 'JanelaEdicao') {
          let objetoParaEnvio = {}
          objetoParaEnvio['observacao'] = this.leObs
          objetoParaEnvio['inativo_cp'] = this.lecpdecujus

          this.$http.post('observacao', objetoParaEnvio)
            .then(response => {
              this.selectedInst.obs.push(response.data)
              this.$toastr.s(
                'Observação enviada com sucesso', 'Sucesso!'
              )
              this.deondeVemObs = ''
              this.dialogNewObs = false
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível enviar a observação!', 'Erro!'
              )
            })
        } else if (this.deondeVemObs === 'JanelaNovoDeCujus') {
          this.lenewobsnewdecujo.push({
            'observacao': this.leObs,
            'id': this.contadorIndexAux++ + '_t'
          })
          this.deondeVemObs = ''
          this.leObs = ''
          this.dialogNewObs = false
        }
      } else {
        this.$toastr.e(
          'É necessário escrever algo no campo Observação', 'Erro!'
        )
      }
    },

    editObs (item) {
      this.objetoObs = item
      this.leObsEdit = item.observacao
      this.dialogEditObs = true
      this.deondeVemObs = 'JanelaDetalhe'
    },

    submeteEditObs () {
      if (this.leObsEdit.length !== 0) {
        if (this.deondeVemObs === 'JanelaDetalhe' || this.deondeVemObs === 'JanelaEdicao') {
          let objetoParaEnvio = {}
          objetoParaEnvio['observacao'] = this.leObsEdit
          objetoParaEnvio['inativo_cp'] = this.objetoObs.inativo_cp

          this.$http.put('observacao/' + this.objetoObs.id, objetoParaEnvio)
            .then(response => {
              for (let i = 0; i < this.selectedInst.obs.length; i++) {
                if (this.selectedInst.obs[i].id === response.data.id) {
                  this.selectedInst.obs.splice(i, 1, response.data)
                }
              }

              this.$toastr.s(
                'Observação alterada com sucesso', 'Sucesso!'
              )
              this.dialogEditObs = false
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível alterar a observação!', 'Erro!'
              )
            })
        } else if (this.deondeVemObs === 'JanelaNovoDeCujus') {
          for (let i = 0; i < this.selectedInst.obs.length; i++) {
            if (this.selectedInst.obs[i].id === this.objetoObs.id) {
              this.selectedInst.obs.splice(i, 1, {'observacao': this.leObsEdit, 'id': this.objetoObs.id})
              this.$toastr.s(
                'Observação alterada com sucesso', 'Sucesso!'
              )
              this.dialogEditObs = false
            }
          }
        }
      } else {
        this.$toastr.e(
          'É necessário escrever algo no campo Observação', 'Erro!'
        )
      }
    }

    // fim obs decujus
  }
}

</script>

<style>
  .v-messages__message {
    color: red;
  }

  .nobullets {
    list-style-type: none; /* Remove bullets */
    padding: 0; /* Remove padding */
    margin: 0; /* Remove margins */
  }
</style>



