<template>
  <!--Movimentações Cadastradas-->
  <div>
    <v-row>

      <v-col>

        <!--title and button-->
        <v-row>
          <v-col>
            <h4>Movimentações do Processo (LEGADO):</h4>
          </v-col>
          <v-col class="text-right" v-if="usuarioLogado.permissoes.indexOf('Administrador') !== -1 || usuarioLogado.permissoes.indexOf('Administrador Geral') !== -1">
            <v-btn @click="openDialogCadastraMov" color="primary"
                   small>
              <v-icon class="mr-4" small>
                mdi-plus-circle
              </v-icon>
              Cadastrar Movimentação
            </v-btn>
          </v-col>
        </v-row>

        <v-data-table
          :headers="headersAndamentoProcessoData"
          :items="editedDeCujus.andamento_processo"
          :items-per-page="-1"
          class="elevation-21 mt-4"
          sort-by="id"
        >
          <!--Template de ajustar datas de Doc -->
          <template v-slot:item.Data_Doc="{ item }">

            {{ formataDataHora(item.Data_Doc, 'Não')}}

          </template>

          <!--Template de ajustar datas de Entrada -->
          <template v-slot:item.DataEntrada="{ item }">

            {{ formataDataHora(item.DataEntrada, 'Não')}}

          </template>

          <!--Template de ajustar resp -->
          <template v-slot:item.resp="{ item }">

            <span v-if="item.user_id === 0"> Não Identificado</span> <span
            v-else>{{ item.resp }}</span>

          </template>

          <!--Template de botões para detalhes, editar e excluir -->
          <template v-slot:item.actions="{ item }">

                              <span v-if="item.user_id === usuarioLogado.id">

                              <!--editar-->
                              <v-tooltip top>
                                <template v-slot:activator="{ on, attrs }">
                                  <v-icon
                                    @click="openDialogEditaAp(item)"
                                    class="mr-2"
                                    small
                                    v-bind="attrs"
                                    v-on="on"
                                  >
                                    mdi-pencil
                                  </v-icon>
                                </template>
                                <span>Editar Movimentação</span>
                              </v-tooltip>

                                <!--Excluir-->
                              <v-tooltip top>
                                <template v-slot:activator="{ on, attrs }">
                                  <v-icon
                                    @click="deleteMovProc(item)"
                                    class="mr-2"
                                    small
                                    v-bind="attrs"
                                    v-on="on"
                                  >
                                    mdi-delete
                                  </v-icon>
                                </template>
                                <span>Excluir movimentação</span>
                              </v-tooltip>

                              </span>

            <span v-else> - </span>
          </template>
        </v-data-table>

        <!--Dialog para adicionar / editar movimentações no processo-->
        <v-dialog max-width="70%" persistent v-model="dialogCadastraMov">

          <v-card>

            <!--title-->
            <v-card-title class="justify-center" primary-title>
              <v-icon
                class="mr-4">
                mdi-file-document-box-plus-outline
              </v-icon>
              {{titleDialogAp}}
            </v-card-title>

            <v-card-text>

              <!--Destino, nr doc, tipo de documento -->
              <v-row>

                <!--destino, nr doc, -->
                <v-col>

                  <!--destino-->
                  <v-row no-gutters>
                    <v-col>
                      <v-text-field
                        dense
                        label="Destino"
                        name="apdestino"
                        outlined
                        placeholder="Insira o destino do processo"
                        required
                        v-model="apdestino"
                      ></v-text-field>

                      <!--alert para Destino em branco-->
                      <v-alert type="error" v-if="apdestino.length === 0">O Campo Destino não pode ficar em
                        branco!
                      </v-alert>

                    </v-col>
                  </v-row>

                  <!--nr doc-->
                  <v-row no-gutters>
                    <v-col>
                      <v-text-field
                        dense
                        label="Nr Doc"
                        name="apnrdoc"
                        outlined
                        placeholder="Insira o número do Documento"
                        required
                        v-model="apnrdoc"
                      ></v-text-field>

                      <!--alert para nr doc em branco-->
                      <v-alert type="error" v-if="apnrdoc.length === 0">O Campo "Nr Doc" não pode ficar em
                        branco!
                      </v-alert>

                    </v-col>
                  </v-row>

                </v-col>

                <!--tipo de doc -->
                <v-col>

                  <!--Tipo de documento-->
                  <v-row no-gutters>
                    <v-col>
                      <v-text-field
                        dense
                        label="Tipo de Documento"
                        name="aptipodoc"
                        outlined
                        placeholder="Insira o Tipo de Documento"
                        required
                        v-model="aptipodoc"
                      ></v-text-field>
                      <!--alert para tipo de documento em branco-->
                      <v-alert type="error" v-if="aptipodoc.length === 0">O Campo Tipo de Documento não pode ficar
                        em branco!
                      </v-alert>

                    </v-col>
                  </v-row>

                </v-col>

              </v-row>

              <!--data do doc e data de entrada-->
              <v-row>
                <!--data do documento-->
                <v-col>
                  <v-row no-gutters>
                    <v-col>
                      <v-menu
                        :close-on-content-click="false"
                        min-width="auto"
                        offset-y
                        ref="menu20"
                        transition="scale-transition"
                        v-model="menu20"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            :messages="apdatadocerror"
                            @blur="apdateDoc = parseDate(apdatadoc)"
                            dense
                            hint="Formato DD/MM/YYYY"
                            label="Data do Documento"
                            name="apdata_doc"
                            outlined
                            placeholder="Insira a data do Documento"
                            prepend-inner-icon="mdi-calendar"
                            required
                            v-bind="attrs"
                            v-model="apdatadoc"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          @input="menu20 = false"
                          v-model="apdateDoc">
                        </v-date-picker>

                      </v-menu>
                    </v-col>
                  </v-row>
                </v-col>
                <!--data de entrada-->
                <v-col>
                  <v-row no-gutters>

                    <v-col>
                      <v-menu
                        :close-on-content-click="false"
                        min-width="auto"
                        offset-y
                        ref="menu21"
                        transition="scale-transition"
                        v-model="menu21"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            :messages="apdataentradaerror"
                            @blur="apdateEntrada = parseDate(apdataentrada)"
                            dense
                            hint="Formato DD/MM/YYYY"
                            label="Data de Entrada do Documento"
                            name="apdata_entrada"
                            outlined
                            placeholder="Insira a data de entrada documento"
                            prepend-inner-icon="mdi-calendar"
                            required
                            v-bind="attrs"
                            v-model="apdataentrada"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          @input="menu21 = false"
                          v-model="apdateEntrada">
                        </v-date-picker>

                      </v-menu>
                    </v-col>

                  </v-row>
                </v-col>
              </v-row>

              <!--despacho-->
              <v-row no-gutters>
                <v-col>
                  <v-textarea
                    dense
                    label="Despacho"
                    name="despacho"
                    outlined
                    placeholder="Insira o despacho"
                    required
                    v-model="apdespacho"
                  ></v-textarea>

                  <!--alert para Despacho em branco-->
                  <v-row no-gutters v-if="apdespacho.length === 0">
                    <v-col>
                      <v-alert type="error">O Campo Despacho não pode ficar em branco!</v-alert>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>

            </v-card-text>

            <!--actions-->
            <v-card-actions class="pb-5">
              <v-spacer></v-spacer>

              <!--fechar-->
              <v-btn @click="dialogCadastraMov = false" color="grey lighten-1">Cancelar</v-btn>
              <span class="pl-5 pr-5"></span>

              <!--enviar-->
              <v-btn @click="doMovProc" class="success">Salvar</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>

          </v-card>

        </v-dialog>

        <!--Dialog para deletar movimentação-->
        <v-dialog max-width="800px" v-model="dialogDeleteap">
          <v-card>
            <v-card-title class="justify-center" primary-title>
              <v-icon
                class="mr-4">
                fa fa-exclamation-triangle
              </v-icon>
              Você tem certeza que quer deletar esta movimentação do processo?
              <v-icon
                class="ml-4">
                fa fa-exclamation-triangle
              </v-icon>

            </v-card-title>
            <v-card-text>
              <div class="text-center">Essa ação é irreversível. Tenha certeza do que está fazendo.</div>
            </v-card-text>
            <v-card-actions class="pb-5">
              <v-spacer></v-spacer>
              <v-btn @click="dialogDeleteap = false" color="grey lighten-1">Cancelar</v-btn>
              <span class="pl-5 pr-5"></span>
              <v-btn @click="deleteMovConfirm" color="red lighten-1">Excluir</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>

      </v-col>
    </v-row>

  </div>
</template>

<script>import moment from 'moment'
import {mapGetters} from 'vuex'

export default {
  data: vm => ({
    headersAndamentoProcessoData: [
      {
        text: 'Destino',
        align: 'start',
        value: 'Destino'
      },
      {
        text: 'Tipo de Doc',
        value: 'Tipo_Doc'
      },
      {
        text: 'Nr Doc',
        value: 'Nr_Doc'
      },
      {
        text: 'Data Doc',
        value: 'Data_Doc'
      },
      {
        text: 'Data Entrada',
        value: 'DataEntrada'
      },
      {
        text: 'Despacho',
        value: 'Despacho'
      },
      {
        text: 'Resp',
        value: 'resp'
      },
      {
        text: 'Acões',
        value: 'actions',
        align: 'center'
      }
    ],
    apdestino: '',
    aptipodoc: '',
    apnrdoc: '',
    menu20: false,
    apdatadocerror: '',
    apdateDoc: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    apdatadoc: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
    menu21: false,
    apdataentradaerror: '',
    apdateEntrada: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    apdataentrada: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
    apdespacho: '',
    apprec: '',
    apcp: '',
    tempIdAp: '',
    titleDialogAp: '',
    indexAp: -1,
    dialogCadastraMov: false,
    dialogDeleteap: false
  }),
  computed: {
    ...mapGetters(['usuarioLogado'])
  },
  watch: {
    apdateDoc (val) {
      this.apdatadoc = this.formatDate(this.apdateDoc)
    },
    apdateEntrada (val) {
      this.apdataentrada = this.formatDate(this.apdateEntrada)
    }
  },
  props: {
    editedDeCujus: Object
  },
  methods: {

    openDialogCadastraMov () {
      this.resetaCamposMov()
      this.dialogCadastraMov = true
      this.titleDialogAp = 'Cadastro de Andamento de Processo'
      this.indexAp = -1
    },

    resetaCamposMov () {
      this.apdestino = ''
      this.aptipodoc = ''
      this.apnrdoc = ''
      this.apdatadocerror = ''
      this.apdateDoc = (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)
      this.apdataentradaerror = ''
      this.apdateEntrada = (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)
      this.apdespacho = ''
      this.apprec = ''
      this.apcp = ''
      this.indexAp = -1
    },

    doMovProc () {
      if (this.apdestino.length === 0 || this.aptipodoc.length === 0 || this.apnrdoc.length === 0 || this.apdespacho.length === 0) {
        this.$toastr.e(
          'Verifique os campos do formulário!', 'Erro!'
        )
      } else {
        let objetoParaEnvio = {}
        objetoParaEnvio['Destino'] = this.apdestino
        objetoParaEnvio['Tipo_Doc'] = this.aptipodoc
        objetoParaEnvio['Nr_Doc'] = this.apnrdoc
        objetoParaEnvio['Data_Doc'] = this.apdatadoc
        objetoParaEnvio['DataEntrada'] = this.apdataentrada
        objetoParaEnvio['Despacho'] = this.apdespacho
        objetoParaEnvio['Prec'] = this.editedDeCujus.PREC
        objetoParaEnvio['CP'] = this.editedDeCujus.CP

        if (this.indexAp === -1) {
          this.$http.post('ap', objetoParaEnvio)
            .then(response => {
              this.editedDeCujus.andamento_processo.push(response.data)
              this.resetaCamposMov()
              this.dialogCadastraMov = false
              this.$toastr.s(
                'Movimentação do processo cadastrada com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível cadastrar a movimentação do processo!', 'Erro!'
              )
            })
        } else {
          this.$http.put('ap/' + this.indexAp, objetoParaEnvio)
            .then(response => {
              for (let i = 0; i < this.editedDeCujus.andamento_processo.length; i++) {
                if (this.editedDeCujus.andamento_processo[i].id === this.indexAp) {
                  this.editedDeCujus.andamento_processo.splice(i, 1, response.data)
                }
              }
              this.resetaCamposMov()
              this.dialogCadastraMov = false
              this.$toastr.s(
                'Movimentação do processo alterada com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível alterar a movimentação do processo!', 'Erro!'
              )
            })
        }
      }
    },

    deleteMovProc (item) {
      this.tempIdAp = item.id
      this.dialogDeleteap = true
    },

    deleteMovConfirm () {
      this.$http.delete('ap/' + this.tempIdAp)
      // eslint-disable-next-line no-return-assign
        .then(() => {
          for (let i = 0; i < this.editedDeCujus.andamento_processo.length; i++) {
            if (this.editedDeCujus.andamento_processo[i].id === this.tempIdAp) {
              this.editedDeCujus.andamento_processo.splice(i, 1)
            }
          }
          this.$toastr.s(
            'Andamento de Processo excluído com sucesso', 'Sucesso!'
          )
          this.tempIdAp = ''
          this.dialogDeleteap = false
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível excluir o andamento do processo!', 'Erro!'
          )
        })
    },

    openDialogEditaAp (item) {
      this.resetaCamposMov()
      this.titleDialogAp = 'Alteração de Andamento de Processo'
      this.dialogCadastraMov = true
      this.indexAp = item.id
      this.apdestino = item.Destino
      this.apdespacho = item.Despacho
      this.apnrdoc = item.Nr_Doc
      this.aptipodoc = item.Tipo_Doc
      this.apprec = item.Prec
      this.apcp = item.CP
      this.apdatadoc = this.formataDataHora(item.Data_Doc, 'Não')
      this.apdataentrada = this.formataDataHora(item.DataEntrada, 'Não')
    },

    // utilitários

    formataDataHora (dataHora, ch) {
      if (dataHora === null) {
        return null
      } else if (dataHora[2] === '/') {
        return dataHora
      } else {
        let d = moment.tz(dataHora, 'America/Manaus')
        let dh
        if (ch === 'Sim') {
          dh = d.format('DD/MM/YYYY ( HH:mm:ss )')
        } else {
          dh = d.format('DD/MM/YYYY')
        }
        return dh
      }
    },

    formatDate (date) {
      if (!date) return null
      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },

    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    }
  }
}
</script>



