<template>
  <Local :locais="locais" />
</template>

<script>import {mapGetters} from 'vuex'
import Local from '../components/locais/Local'

export default {
  components: {
    Local
  },
  data () {
    return {
      locais: []
    }
  },
  mounted () {
    this.$http.get('ua')
      .then(response => {
        this.locais = response.data
      })
      .catch(erro => console.log(erro))
  },
  computed: {

    ...mapGetters(['usuarioLogado'])

  }
}
</script>



