<template>
  <div>
    <!--observações-->
    <v-alert color="yellow">

      <!--title e btn-->
      <v-row>

        <!--Title-->
        <v-col>
          <h2>Observações</h2>
        </v-col>

        <!--Btn add obs-->
        <v-col class="text-right">
          <v-btn @click="showHideObsInativo" class="secondary" small>
            {{textBtnShowHide}} Obs
          </v-btn>
          <v-btn @click="openNewObs(instituidor.CP)" class="primary" small v-if="obsVisibility">
            <v-icon class="mr-5" small>mdi-message-plus</v-icon>
            Adicionar Obs
          </v-btn>
        </v-col>

      </v-row>

      <!--espaço para observacoes-->
      <v-row v-if="obsVisibility">

        <v-col>

          <!--DataTable-->
          <v-data-table
            :headers="headersObs"
            :items="instituidor.obs"
            :items-per-page="-1"
            class="elevation-21 mt-4"
            sort-by="nome"
          >

            <!--Template de ajustar datas de inclusão -->
            <template v-slot:item.created_at="{ item }">

              {{ formataDataHora(item.created_at, 'Sim')}}

            </template>

            <!--Template de botões para editar e excluir -->
            <template v-slot:item.actionsObs="{ item }">

              <!--editar-->
              <v-tooltip top v-if="usuarioLogado.id === item.user_id">
                <template v-slot:activator="{ on, attrs }">
                  <v-icon
                    @click="editObs(item)"
                    class="mr-2"
                    small
                    v-bind="attrs"
                    v-on="on"
                  >
                    mdi-pencil
                  </v-icon>
                </template>
                <span>Editar Observação</span>
              </v-tooltip>

              <!--Excluir-->
              <v-tooltip top v-if="usuarioLogado.id === item.user_id">
                <template v-slot:activator="{ on, attrs }">
                  <v-icon
                    @click="excluiObs(item)"
                    class="mr-2"
                    small
                    v-bind="attrs"
                    v-on="on"
                  >
                    mdi-delete
                  </v-icon>
                </template>
                <span>Excluir Observação</span>
              </v-tooltip>

            </template>

          </v-data-table>

          <!--Dialog para cadastrar / editar Obs-->
          <v-dialog max-width="70%" persistent v-model="dialogNewObs">

            <v-card>

              <!--title-->
              <v-card-title class="justify-center" primary-title>
                <v-icon
                  class="mr-4">
                  mdi-message-plus
                </v-icon>
                {{leTitleObsDialog}} de Observação

              </v-card-title>

              <v-card-text>

                <!--Obs-->
                <v-row>
                  <v-col>
                    <v-textarea
                      dense
                      label="Observação"
                      name="leObs"
                      outlined
                      placeholder="Insira a Observação"
                      required
                      v-model="leObs"
                    ></v-textarea>
                  </v-col>
                </v-row>

                <!--alert para obs em branco-->
                <v-row no-gutters v-if="leObs.length === 0">
                  <v-col>
                    <v-alert type="error">O Campo Observação não pode ficar em branco!</v-alert>
                  </v-col>
                </v-row>

              </v-card-text>

              <!--actions-->
              <v-card-actions class="pb-5">
                <v-spacer></v-spacer>

                <!--fechar-->
                <v-btn @click="dialogNewObs = false" color="grey lighten-1">Cancelar</v-btn>
                <span class="pl-5 pr-5"></span>

                <!--enviar-->
                <v-btn @click="submeteObs" class="success">Salvar</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>

            </v-card>

          </v-dialog>

          <!--Dialog para deletar obs-->
          <v-dialog max-width="800px" v-model="dialogDeleteObs">
            <v-card>
              <v-card-title class="justify-center" primary-title>
                <v-icon
                  class="mr-4">
                  fa fa-exclamation-triangle
                </v-icon>
                Você tem certeza que quer deletar esta Observação?
                <v-icon
                  class="ml-4">
                  fa fa-exclamation-triangle
                </v-icon>

              </v-card-title>
              <v-card-text>
                <div class="text-center">Essa ação é irreversível. Tenha certeza do que está fazendo.</div>
              </v-card-text>
              <v-card-actions class="pb-5">
                <v-spacer></v-spacer>
                <v-btn @click="dialogDeleteObs = false" color="grey lighten-1">Cancelar</v-btn>
                <span class="pl-5 pr-5"></span>
                <v-btn @click="deleteObsConfirm" color="red lighten-1">Excluir</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>

        </v-col>

      </v-row>

    </v-alert>
  </div>
</template>

<script>import moment from 'moment'
import {mapGetters} from 'vuex'

export default {
  data: vm => ({
    headersObs: [
      {
        text: 'Observação',
        align: 'start',
        value: 'observacao'
      },
      {
        text: 'Responsável',
        align: 'start',
        value: 'resp'
      },
      {
        text: 'Data/Hora',
        align: 'start',
        value: 'created_at'
      },
      {
        text: 'Actions',
        value: 'actionsObs',
        align: 'center',
        sortable: false
      }
    ],
    dialogNewObs: false,
    dialogDeleteObs: false,
    dialogEditObs: false,
    leObs: '',
    leTitleObsDialog: '',
    lecpdecujus: '',
    leidObs: -1,
    obsVisibility: false,
    textBtnShowHide: 'Exibir'
  }),
  computed: {
    ...mapGetters(['usuarioLogado'])
  },
  props: {
    instituidor: Object
  },
  methods: {
    openNewObs (cp) {
      this.lecpdecujus = cp
      this.leObs = ''
      this.leTitleObsDialog = 'Cadastro'
      this.dialogNewObs = true
      this.leidObs = -1
    },

    editObs (item) {
      console.log(item)
      this.lecpdecujus = item.cp
      this.leObs = item.observacao
      this.leTitleObsDialog = 'Edição'
      this.dialogNewObs = true
      this.leidObs = item.id
    },

    submeteObs () {
      if (this.leObs.length !== 0) {
        let objetoParaEnvio = {}
        objetoParaEnvio['observacao'] = this.leObs
        objetoParaEnvio['inativo_cp'] = this.lecpdecujus

        if (this.leidObs === -1) {
          this.$http.post('observacao', objetoParaEnvio)
            .then(response => {
              this.instituidor.obs.push(response.data)
              this.$toastr.s(
                'Observação enviada com sucesso', 'Sucesso!'
              )
              this.dialogNewObs = false
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível enviar a observação!', 'Erro!'
              )
            })
        } else {
          this.$http.put('observacao/' + this.leidObs, objetoParaEnvio)
            .then(response => {
              for (let i = 0; i < this.instituidor.obs.length; i++) {
                if (this.instituidor.obs[i].id === this.leidObs) {
                  this.instituidor.obs.splice(i, 1, response.data)
                }
              }
              this.$toastr.s(
                'Observação alterada com sucesso', 'Sucesso!'
              )
            }, err => {
              console.log(err)
              this.$toastr.e(
                'Não foi possível alterar a observação!', 'Erro!'
              )
            })
          this.dialogNewObs = false
        }
      } else {
        this.$toastr.e(
          'É necessário escrever algo no campo Observação', 'Erro!'
        )
      }
    },

    deleteObsConfirm () {
      this.$http.delete('observacao/' + this.objetoObs.id)
      // eslint-disable-next-line no-return-assign
        .then(() => {
          for (let i = 0; i < this.instituidor.obs.length; i++) {
            if (this.instituidor.obs[i].id === this.objetoObs.id) {
              this.instituidor.obs.splice(i, 1)
            }
          }
          this.$toastr.s(
            'Observação excluída com sucesso', 'Sucesso!'
          )
          this.dialogDeleteObs = false
        }, err => {
          console.log(err)
          this.$toastr.e(
            'Não foi possível excluir a observação!', 'Erro!'
          )
        })
    },

    excluiObs (item) {
      this.objetoObs = item
      this.dialogDeleteObs = true
    },

    showHideObsInativo () {
      if (this.obsVisibility) {
        this.textBtnShowHide = 'Exibir '
        this.obsVisibility = false
      } else {
        this.textBtnShowHide = 'Ocultar '
        this.obsVisibility = true
      }
    },

    // utilitários

    formataDataHora (dataHora, ch) {
      if (dataHora === null) {
        return null
      } else if (dataHora[2] === '/') {
        return dataHora
      } else {
        let d = moment.tz(dataHora, 'America/Manaus')
        let dh
        if (ch === 'Sim') {
          dh = d.format('DD/MM/YYYY ( HH:mm:ss )')
        } else {
          dh = d.format('DD/MM/YYYY')
        }
        return dh
      }
    }
  }
}
</script>



